import { actionTypes } from '@servicenow/ui-core';
import { isEqual } from 'lodash';
import { TIME_AGO_PRESETS, getTimeAgoByValue } from '../utils/timeAgo';

import {
	PRESET_SELECTED,
	FETCH_CALENDAR,
	FETCH_CALENDAR_SUCCESS,
	FETCH_CALENDAR_FAILURE,
	FETCH_ABSOLUTE,
	FETCH_ABSOLUTE_SUCCESS,
	FETCH_ABSOLUTE_FAILURE
} from './constants';

import {
	fetchCalendarEffect,
	fetchCalendarSuccessEffect,
	fetchCalendarFailureEffect,
	fetchAbsoluteEffect,
	fetchAbsoluteSuccessEffect,
	fetchAbsoluteFailureEffect,
	dispatchTimeAgo
} from './effects';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export default {
	[PRESET_SELECTED]: {
		effect: ({ state, action, dispatch }) => {
			let {
				timeAgo: { label, start, end, between }
			} = action.payload;

			const { onValueChange, between: isBetween, absolute } = state.properties;

			if (isBetween) {
				end = between;
			}

			if (absolute) {
				dispatch(FETCH_ABSOLUTE, { from_date: start, to_date: end });
			} else {
				const presetSelected = {
					label,
					relative: { start, end },
					absolute: { start: '', end: '' }
				};
				dispatchTimeAgo(dispatch, onValueChange, presetSelected);
			}
		},
		stopPropagation: true
	},
	[COMPONENT_BOOTSTRAPPED]: ({
		properties: { value = '', tableName = '', format },
		dispatch,
		updateState
	}) => {
		//initialize with timeAgoRelative Values
		//Append more for Business Calendar
		let timeAgoPresets = TIME_AGO_PRESETS;
		const selectedTimeAgo = getTimeAgoByValue(timeAgoPresets, format, value);

		//get business calendars from tableName
		if (tableName) {
			dispatch(FETCH_CALENDAR, { table_name: tableName });
		}

		updateState({
			selectedTimeAgo,
			timeAgoPresets: TIME_AGO_PRESETS
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, value, previousValue }
		},
		state: { timeAgoPresets },
		updateState,
		properties: { format }
	}) => {
		if (name === 'value' && !isEqual(value, previousValue)) {
			updateState({
				selectedTimeAgo: getTimeAgoByValue(timeAgoPresets, format, value)
			});
		}
	},

	[FETCH_CALENDAR]: {
		...fetchCalendarEffect,
		stopPropagation: true
	},
	[FETCH_CALENDAR_SUCCESS]: {
		effect: fetchCalendarSuccessEffect,
		stopPropagation: true
	},
	[FETCH_CALENDAR_FAILURE]: {
		effect: fetchCalendarFailureEffect,
		stopPropagation: true
	},

	[FETCH_ABSOLUTE]: {
		...fetchAbsoluteEffect,
		stopPropagation: true
	},
	[FETCH_ABSOLUTE_SUCCESS]: {
		effect: fetchAbsoluteSuccessEffect,
		stopPropagation: true
	},
	[FETCH_ABSOLUTE_FAILURE]: {
		effect: fetchAbsoluteFailureEffect,
		stopPropagation: true
	}
};
