import { time } from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';
import {
	QUARTERS_ARRAY,
	QUARTERLY_FORMAT,
	AFFIRMATIVE_KEYS,
	NAVIGATION_KEYS,
	INIT_HIGHLIGHTED_DATE,
	SET_VISIBLE_YEAR,
	SET_DATE
} from '../../constants';
import { some, noop } from 'lodash';
import moment from 'moment';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

export const QuarterlyGrid = ({
	displayedYear,
	highlightedQuarter,
	hoveredQuarter,
	start,
	end,
	updateState,
	language,
	dispatch,
	isRangePicker
}) => {
	let isFocusedFromClick = false;
	const handleQuarterSelection = (quarter, year) => () => {
		const date = time(language)(`${quarter} ${year}`, QUARTERLY_FORMAT);
		dispatch(SET_DATE, { date });
		updateState({ highlightedQuarter: date });
	};

	const resetHighlighted = () => {
		// reset back to start date or current date
		updateState({
			hoveredQuarter: null
		});
	};

	const getQuarterGrid = ({ year, selected = [] }) => {
		return QUARTERS_ARRAY.map((quarter, index) => {
			const isYearAndQuarterSame = s =>
				s &&
				s.isValid() &&
				s.year() === parseInt(year) &&
				s.quarter() === index + 1;

			const isSelected = some(selected, isYearAndQuarterSame);
			const isHighlighted = isYearAndQuarterSame(highlightedQuarter);

			const currentQuarter = moment(`${index + 1} ${year}`, QUARTERLY_FORMAT);
			const isCurrentQuarter = isYearAndQuarterSame(moment());

			const [start, end] = selected;
			const isInRange =
				currentQuarter.isBetween(start, end) ||
				// don't highlight when end of range is selected
				(end &&
					!end.isValid() &&
					currentQuarter.isBetween(
						start,
						hoveredQuarter || highlightedQuarter
					));

			const handleSelection = !isSelected
				? handleQuarterSelection(index + 1, year)
				: noop;

			const handleKeyDown = event => {
				if (AFFIRMATIVE_KEYS.includes(event.key)) {
					handleSelection();
				}
			};

			const updateHighlighted = () => {
				updateState({
					hoveredQuarter: moment(`${index + 1} ${year}`, QUARTERLY_FORMAT)
				});
			};

			return (
				<span
					id={`${index + 1}-${year}`}
					class={{
						'quarter-item': true,
						'is-highlighted': isHighlighted,
						'is-selected': isSelected,
						'is-in-range': isRangePicker ? isInRange : false,
						'is-current-quarter': isCurrentQuarter
					}}
					on-click={handleSelection}
					on-mousedown={() => {
						isFocusedFromClick = true;
					}}
					on-keydown={handleKeyDown}
					on-mouseenter={updateHighlighted}
					role="button"
					aria-label={`Quarter ${index + 1} ${year}`}
				>
					{quarter}
				</span>
			);
		});
	};

	const leftQuarterGrid = getQuarterGrid({
		year: displayedYear,
		selected: [start, end]
	});

	const rightQuarterGrid = isRangePicker
		? getQuarterGrid({
				year: displayedYear + 1,
				selected: [start, end]
		  })
		: null;

	const handleNavigation = e => {
		/*
		state:
			highlightedDate: start | first_cell

		0. On navigation, update `highlightedQuarter`
		1a. On enter:
			start/end set to highlightedQuarter
		1b. On click:
			start/end set to selected, highlighteQuarter set to selected
		*/

		const { key } = e;

		const getNextHighlightedQuarter = key => {
			let operation, increment;
			switch (key) {
				case 'ArrowRight':
					operation = 'add';
					increment = 1;
					break;
				case 'ArrowLeft':
					operation = 'subtract';
					increment = 1;
					break;
				case 'ArrowDown':
					operation = 'add';
					increment = 4;
					break;
				case 'ArrowUp':
					operation = 'subtract';
					increment = 4;
					break;
				default:
					return highlightedQuarter;
			}

			return highlightedQuarter.clone()[operation](increment, 'quarter');
		};

		const nextHighlightedQuarter = getNextHighlightedQuarter(key);

		if (NAVIGATION_KEYS.includes(key)) {
			updateState({
				highlightedQuarter: nextHighlightedQuarter
			});

			// If the next highlighted quarter is going prior to the left-side quarter,
			// go back an additional year so it ends up wrapping rather than jumping
			if (nextHighlightedQuarter.year() < displayedYear) {
				dispatch(SET_VISIBLE_YEAR, {
					year: isRangePicker
						? nextHighlightedQuarter.year() - 1
						: nextHighlightedQuarter.year()
				});
			} else if (
				(nextHighlightedQuarter.year() !== displayedYear &&
					nextHighlightedQuarter.year() !== displayedYear + 1) ||
				(!isRangePicker && nextHighlightedQuarter.year() !== displayedYear)
			) {
				dispatch(SET_VISIBLE_YEAR, {
					year: nextHighlightedQuarter.year()
				});
			}

			e.preventDefault();

			return;
		}

		if (AFFIRMATIVE_KEYS.includes(key)) {
			e.preventDefault();
			dispatch(SET_DATE, { date: highlightedQuarter });
		}
	};

	return (
		<div
			className="quarterly-view-grid"
			tabindex="0"
			on-focus={() => {
				if (isFocusedFromClick) {
					isFocusedFromClick = false;
					return;
				}

				dispatch(INIT_HIGHLIGHTED_DATE, { displayedYear });
			}}
			role="application"
			onkeydown={handleNavigation}
			on-mouseleave={resetHighlighted}
			aria-activedescendant={
				highlightedQuarter.isValid()
					? `${highlightedQuarter.quarter()}-${highlightedQuarter.year()}`
					: undefined
			}
			aria-label={
				highlightedQuarter.isValid()
					? t(
							'Selected quarter: {0}. Use arrow keys to adjust the selected quarter.',
							highlightedQuarter.format(QUARTERLY_FORMAT)
					  )
					: undefined
			}
		>
			<div className="quarter-row">{leftQuarterGrid}</div>

			{isRangePicker ? (
				<Fragment>
					<span className="spacer" />
					<div className="quarter-row">{rightQuarterGrid}</div>
				</Fragment>
			) : null}
		</div>
	);
};
