import { isFunction, isEmpty, pickBy } from 'lodash';
import { t } from 'sn-translate';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';

import {
	PRESET_VALUE_CHANGED,
	PRESET_MESSAGE_UPDATED,
	FETCH_CALENDAR_SUCCESS,
	FETCH_CALENDAR_FAILURE,
	FETCH_ABSOLUTE_SUCCESS,
	FETCH_ABSOLUTE_FAILURE,
	ENDPOINT_BUSINESS_CALENDAR,
	ENDPOINT_TIMEAGO_ABSOLUTE
} from './constants';

import { getTimeAgoByValue, SCHEDULE_PRESET_LABEL } from '../utils/timeAgo';
const SCHEDULE_PRESET_TYPE = 'schedule';

export const dispatchTimeAgo = (dispatch, onValueChange, presetSelected) => {
	if (isFunction(onValueChange)) {
		adaptCallbackForLegacyAPI(onValueChange)(presetSelected);
	}
	dispatch(PRESET_VALUE_CHANGED, presetSelected);
};

const getBusinessCalendars = results => {
	let schedulePresets = [];
	const respObj = results ? results.result : {};
	if (!isEmpty(respObj)) {
		const scheduleGroupObj = respObj.dateChoiceDisplayValues
			? respObj.dateChoiceDisplayValues.find(
					v => v.type === SCHEDULE_PRESET_TYPE
			  )
			: {};
		const scheduleGroups =
			scheduleGroupObj && scheduleGroupObj.values
				? scheduleGroupObj.values.map(g => g.value)
				: [];
		if (scheduleGroups.length) {
			const timeAgoDates = respObj.timeAgoDates;
			const schedulePresetObj = pickBy(timeAgoDates, (v, k) =>
				scheduleGroups.includes(k)
			);
			schedulePresets = Object.values(schedulePresetObj).map(preset => {
				return {
					group: SCHEDULE_PRESET_LABEL,
					label: preset.label,
					start: preset.before,
					end: preset.after,
					between: preset.between
				};
			});
		}
	}
	return schedulePresets;
};

export const fetchCalendarEffect = createHttpEffect(
	ENDPOINT_BUSINESS_CALENDAR,
	{
		method: 'GET',
		batch: false,
		queryParams: ['table_name'],
		successActionType: FETCH_CALENDAR_SUCCESS,
		errorActionType: FETCH_CALENDAR_FAILURE
	}
);

export const fetchAbsoluteEffect = createHttpEffect(ENDPOINT_TIMEAGO_ABSOLUTE, {
	method: 'GET',
	batch: false,
	queryParams: ['from_date', 'to_date'],
	successActionType: FETCH_ABSOLUTE_SUCCESS,
	errorActionType: FETCH_ABSOLUTE_FAILURE
});

export const fetchCalendarSuccessEffect = ({
	state: {
		selectedTimeAgo,
		timeAgoPresets,
		properties: { value, format }
	},
	updateState,
	action: { payload: results }
}) => {
	let schedulePresets = getBusinessCalendars(results);
	if (schedulePresets.length) {
		if (isEmpty(selectedTimeAgo)) {
			selectedTimeAgo = getTimeAgoByValue(schedulePresets, format, value);
		}
		updateState({
			selectedTimeAgo,
			timeAgoPresets: [...timeAgoPresets, ...schedulePresets]
		});
	}
};

export const fetchCalendarFailureEffect = ({
	state: {
		properties: { tableName }
	},
	dispatch
}) => {
	const message = {
		type: 'info',
		message: t(`Unable to fetch business calendars for table '{0}'`, tableName)
	};
	dispatch(PRESET_MESSAGE_UPDATED, message);
};

export const fetchAbsoluteSuccessEffect = ({
	state: {
		properties: { onValueChange },
		selectedTimeAgo: { label, start, end }
	},
	action: { payload: results },
	dispatch
}) => {
	const respObj = results ? results.result : {};
	if (!isEmpty(respObj)) {
		const absoluteStart = respObj.fromDate;
		const absoluteEnd = respObj.toDate;
		const presetSelected = {
			label,
			relative: { start, end },
			absolute: { start: absoluteStart, end: absoluteEnd }
		};
		dispatchTimeAgo(dispatch, onValueChange, presetSelected);
	}
};

export const fetchAbsoluteFailureEffect = ({
	state: {
		properties: { onValueChange },
		selectedTimeAgo: { label, start, end }
	},
	dispatch
}) => {
	const message = {
		type: 'error',
		message: t(`Unable to resolve absolute dates for '{0}'`, label)
	};
	dispatch(PRESET_MESSAGE_UPDATED, message);

	const presetSelected = {
		label,
		relative: { start, end },
		absolute: { start: '', end: '' }
	};
	dispatchTimeAgo(dispatch, onValueChange, presetSelected);
};
