import moment from 'moment';

import {
	YEARLY_FORMAT,
	YEARLY_VISIBLE_YEARS,
	INVALID_MOMENT
} from '../../constants';
import { YearlyGrid } from '../../grids/yearly/YearlyGrid';
import { YearlyHeader } from './YearlyHeader';
import BottomBar from '../../bottom-bar';

const CURRENT_YEAR = moment();
const MIN_VISIBLE_YEAR = moment({
	year: CURRENT_YEAR.year() - (CURRENT_YEAR.year() % YEARLY_VISIBLE_YEARS)
});
const MAX_VISIBLE_YEAR = moment({
	year: MIN_VISIBLE_YEAR.year() + YEARLY_VISIBLE_YEARS
});

export default (
	{
		highlightedYear = INVALID_MOMENT,
		hoveredYear = INVALID_MOMENT,
		visibleYear,
		properties: { value, bottomBar, onOk, onCancel }
	},
	{ dispatch, updateState }
) => {
	if (!visibleYear) {
		visibleYear = value ? moment(value, YEARLY_FORMAT) : moment();

		// Make sure the selected year is visible
		const isVisible = visibleYear.isBetween(
			MIN_VISIBLE_YEAR,
			MAX_VISIBLE_YEAR,
			'year',
			'[]'
		);
		updateState({ visibleYear: visibleYear.year(), shouldRender: !isVisible });
	}

	return (
		<div className="yearly-view">
			<YearlyHeader />
			<YearlyGrid
				dispatch={dispatch}
				updateState={updateState}
				highlightedYear={highlightedYear}
				hoveredYear={hoveredYear}
				displayedYear={moment(visibleYear, YEARLY_FORMAT).year()}
				start={moment(value, YEARLY_FORMAT)}
			/>
			{bottomBar ? <BottomBar onOk={onOk} onCancel={onCancel} /> : null}
		</div>
	);
};
