import React from 'react';
import { find } from 'lodash';

import { t } from 'sn-translate';
import { time } from '@devsnc/sn-controls-common';

import { Week } from './Week';

const WeekNumber = ({ weekNumber }) => (
	<div key={weekNumber} className="now-record-date-picker-month-week-number">
		{weekNumber}
	</div>
);

export const WeekRow = ({
	year,
	visibleMonth,
	visibleStart,
	disabledWeek,
	week,
	weekIndex,
	weekOfYear,
	tabIndex,
	className,
	showAdjacentMonthDays,
	language,
	onSelectDate,
	highlightedDate,
	onHighlightedDate,
	onEndDayHover,
	onlyStart,
	format,
	rangePicker
}) => {
	const firstVisibleDate = find(week, { disabled: false });

	const handleSelection = (week, weekIndex) => () => {
		if (firstVisibleDate) {
			const day = firstVisibleDate.day;
			let disabledDay = find(week, { disabled: true });
			let selectedDate = visibleMonth.clone().date(day);
			///pad 1 year for last week of year
			if (weekIndex > 0 && disabledDay && visibleMonth.month() == 11) {
				selectedDate.add(1, 'year');
			}
			onSelectDate(selectedDate);
		}
	};

	const handleMouseEnter = () => {
		if (onlyStart && firstVisibleDate) {
			const selectedDate = time(language)({ year }).week(weekOfYear);
			if (selectedDate) {
				if (visibleMonth.isSame(selectedDate, 'month')) {
					onHighlightedDate(selectedDate);
					onEndDayHover(selectedDate);
				}
			}
		}
	};

	const handleMouseLeave = () => {
		if (onlyStart && firstVisibleDate) {
			onEndDayHover('');
		}
	};

	const weekNumber = disabledWeek
		? weekIndex
			? visibleMonth
					.clone()
					.add(1, 'month')
					.date(week[0].day)
					.week()
			: visibleMonth
					.clone()
					.subtract(1, 'month')
					.date(week[0].day)
					.week()
		: visibleStart.week();

	const disabledRow = disabledWeek ? '-disabled' : '';

	if (!firstVisibleDate) {
		className += ' is-disabled-week';
	}

	return (
		<div className="now-record-date-picker-weekly-month-week">
			<WeekNumber weekNumber={weekNumber} />
			<div
				id={`W${weekOfYear} ${year}${disabledRow}`}
				role="button"
				className={className}
				onClick={handleSelection(week, weekIndex, weekOfYear)}
				onMouseEnter={handleMouseEnter}
				onMouseLeave={handleMouseLeave}
				{...tabIndex}
				aria-label={
					highlightedDate && highlightedDate.isValid()
						? t(
								'Selected Week: {0}. Use arrow keys to adjust the selected week.',
								highlightedDate.format(format)
						  )
						: t('Use arrow keys to select a Week Range.')
				}
			>
				<Week
					week={week}
					rangePicker={rangePicker}
					showAdjacentMonthDays={showAdjacentMonthDays}
				/>
			</div>
		</div>
	);
};
