import moment from 'moment';
import { chunk, range, map, noop } from 'lodash';

import { t } from 'sn-translate';

import {
	AFFIRMATIVE_KEYS,
	NAVIGATION_KEYS,
	YEARLY_FORMAT,
	YEARLY_YEARS_PER_ROW,
	YEARLY_VISIBLE_YEARS,
	INIT_HIGHLIGHTED_DATE,
	SET_VISIBLE_YEAR,
	SET_DATE
} from '../../constants';

/**
 * Creates the year grid
 */
export const YearlyGrid = ({
	dispatch,
	updateState,
	hoveredYear,
	displayedYear,
	highlightedYear,
	start,
	end
}) => {
	let isYearClicked = false;
	const lowerYear = displayedYear - (displayedYear % YEARLY_VISIBLE_YEARS);
	const upperYear = lowerYear + YEARLY_VISIBLE_YEARS;
	const yearRows = chunk(range(lowerYear, upperYear), YEARLY_YEARS_PER_ROW);
	const currentYear = moment().year();

	const handleYearSelection = year => () => {
		const date = moment({ year });
		dispatch(SET_DATE, { date });
		dispatch(SET_VISIBLE_YEAR, { year });
		// We reset highligthed year to this date so that there is no random highlighted date
		// when we specifically click on a date
		updateState({ highlightedYear: date });
	};

	const handleFocus = () => {
		if (isYearClicked) {
			isYearClicked = false;
			return;
		}

		dispatch([INIT_HIGHLIGHTED_DATE], {
			visibleRange: { lowerYear, upperYear }
		});
	};
	const resetHighlighted = () => updateState({ hoveredYear: null });
	const handleNavigation = event => {
		const { key } = event;
		const getNextHighlightedYear = key => {
			let increment;
			switch (key) {
				case 'ArrowRight':
					increment = 1;
					break;
				case 'ArrowLeft':
					increment = -1;
					break;
				case 'ArrowDown':
					increment = YEARLY_YEARS_PER_ROW;
					break;
				case 'ArrowUp':
					increment = -YEARLY_YEARS_PER_ROW;
					break;
				default:
					return highlightedYear;
			}

			return highlightedYear.clone().add(increment, 'years');
		};

		if (NAVIGATION_KEYS.includes(key)) {
			const nextHighlightedYear = getNextHighlightedYear(key);
			updateState({ highlightedYear: nextHighlightedYear });

			if (nextHighlightedYear.year() < lowerYear) {
				dispatch(SET_VISIBLE_YEAR, {
					year: Number(displayedYear) - YEARLY_VISIBLE_YEARS
				});
			} else if (nextHighlightedYear.year() >= upperYear) {
				dispatch(SET_VISIBLE_YEAR, {
					year: Number(displayedYear) + YEARLY_VISIBLE_YEARS
				});
			}

			event.preventDefault();
		} else if (AFFIRMATIVE_KEYS.includes(key)) {
			event.preventDefault();
			dispatch(SET_DATE, { date: highlightedYear });
		}
	};

	return (
		<div
			className="year-grid"
			tabindex="0"
			role="application"
			aria-label={
				highlightedYear.isValid()
					? t(
							'Selected date: {0}. Use arrow keys to adjust the selected date.',
							highlightedYear.format(YEARLY_FORMAT)
					  )
					: undefined
			}
			aria-activedescendant={
				highlightedYear.isValid()
					? `year-cell-${highlightedYear.year()}`
					: undefined
			}
			on-mouseleave={resetHighlighted}
			on-keydown={handleNavigation}
			on-focus={handleFocus}
		>
			{yearRows.map(row => (
				<div className="year-row">
					{map(row, year => {
						const yearBeingConsidered = moment({ year });

						const isSelected =
							year === start.year() || (end && year === end.year());

						const isHighlighted =
							highlightedYear.isValid() &&
							yearBeingConsidered.year() === highlightedYear.year();

						const focusedYear =
							hoveredYear && hoveredYear.isValid()
								? hoveredYear
								: highlightedYear;

						const isInRange =
							end &&
							yearBeingConsidered.isBetween(
								start,
								end.isValid() ? end : focusedYear
							);

						const handleSelection = !isSelected
							? handleYearSelection(year)
							: noop;

						const updateHovered = () => {
							updateState({
								hoveredYear: moment({ year })
							});
						};

						return (
							<span
								id={`year-cell-${year}`}
								on-mousedown={() => {
									isYearClicked = true;
								}}
								class={{
									'year-cell': true,
									'is-current-year': currentYear === year,
									'is-selected': isSelected,
									'is-highlighted': isHighlighted,
									'is-in-range': isInRange && !isSelected
								}}
								on-click={handleSelection}
								on-mouseenter={updateHovered}
								role="button"
								aria-label={year}
							>
								{year}
							</span>
						);
					})}
				</div>
			))}
		</div>
	);
};
