import { createCustomElement } from '@servicenow/ui-core';
import NowMiniCalendar from './NowMiniCalendar';
import {
	getDisabledDays,
	isDateIncluded,
	getFormat
} from './utils/calendarUtils';

import react from '@servicenow/ui-renderer-react';
import { transformState, ForwardProperties } from '@devsnc/sn-controls-common';

import * as constants from './constants';
import { CalendarView } from './CalendarView';
export { Standard } from './views/standard';
export { QuarterlyGrid } from './grids/quarterly/QuarterlyGrid';
export { Yearly } from './views/yearly';
export { MonthlyGrid } from './grids/monthly/MonthlyGrid';

export { CalendarView, getDisabledDays, constants, isDateIncluded, getFormat };
export default NowMiniCalendar;

/**
 *
 * Calendar component allowing users to select a date and optional time
 *
 * ```jsx
 * <now-record-mini-calendar value="2020-02-02" />
 * ```
 *
 * With time picker
 *
 * ```jsx
 * <now-record-mini-calendar value="2020-02-02 2:45 pm" timePicker />
 * ```
 *
 * @seismicElement now-record-mini-calendar
 * @summary Calendar component allowing users to select a date and optional time
 *
 * */
createCustomElement('now-record-mini-calendar', {
	type: react,
	view: ForwardProperties(NowMiniCalendar),
	styles: NowMiniCalendar.style,
	properties: {
		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		autofocus: {},
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},
		/**
		 * Array of disabled dates defined as
		 * `[ (DATE | DATE_RANGE)* ]`
		 *
		 * `DATE`:
		 * 	Date string matching `format` property
		 *
		 * `DATE_RANGE`:
		 * 	{ start: `DATE`, end: `DATE` } | { after: `DATE` } | { before: `DATE` }
		 *
		 * where `start`/`end` are inclusive and `after`/`before` are exclusive
		 * eg. [ '13-01-2020', { start: '15-01-2020', end: '20-01-2020' }, { before: '05-01-2020' }, { after: '25-01-2020' }]
		 *
		 * @type { Array < string | {start: string, end: string} | {before: string} | {after: string} > }
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 */
		disabledDates: {
			default: [],
			schema: {
				type: 'array',
				items: [
					{ type: 'string' },
					{
						type: 'object',
						properties: { start: { type: 'string' }, end: { type: 'string' } },
						required: ['start', 'end']
					},
					{ type: 'object', properties: { before: { type: 'string' } } },
					{ type: 'object', properties: { after: { type: 'string' } } }
				]
			}
		},
		/**
		 * What day of the week to begin the calendar. 0 = Sunday, 1 = Monday, etc.
		 * @type {(0| 1| 2| 3| 4| 5| 6)}
		 */
		firstDayOfWeek: {},
		/**
		 * Format describing how to serialize/deserialize the `value` prop.
		 * This property is only relevant for `type='standard'` and is ignored for other views.
		 *
		 * When not provided, the default format is one of:
		 *
		 * - When showing a time picker: `YYYY-MM-DD hh:mm:ss a`
		 * - When not showing a time picker: `YYYY-MM-DD`
		 * - For weekly type, `[W]ww YYYY`
		 * - For yearly type, `YYYY`
		 *
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 * @type {string}
		 */
		format: {},
		/**
		 * Please use `timePicker` instead
		 *
		 * @type {boolean}
		 * @private
		 */
		hasTimePicker: {},

		/**
		 * Whether to show the time picker in addition to the calendar
		 * @type {boolean}
		 */
		timePicker: { default: false },
		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		invalid: { default: false },
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},
		/**
		 * User's preferred language
		 * @type {string}
		 */
		language: { default: 'en' },
		/**
		 * Array of message objects that must contain type and message itself.
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: { default: [] },
		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},
		/**
		 * Callback when user selects `Cancel` button.
		 *
		 * @param {SyntheticEvent} event React synthetic click event
		 * @type {function}
		 * @private
		 */
		onCancel: {},
		/**
		 * Callback when user selects `Ok` button.
		 *
		 * @param {SyntheticEvent} event React synthetic click event
		 * @type {function}
		 * @private
		 */
		onOk: {},
		/**
		 * Callback used when an intermediate value has changed.
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},
		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * Params
		 *
		 * `type:object` event - The event payload
		 *
		 * `type:string` event.name - The name of the component that triggered the event
		 *
		 * `type:any` event.value - The new value for the component
		 *
		 * `type:string` event.displayValue - The human-readable represenation of the value
		 *
		 * `type:object` event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 *
		 * `type:string` event.error.type - Error type
		 *
		 * `type:string` event.error.message - Description of why the value is invalid
		 *
		 * @type {function}
		 * @private
		 */
		onValueChange: {},
		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {boolean}
		 * @private
		 */
		reversed: { default: false },
		/**
		 * Type determines the display view of the date-range-picker.
		 * Supported values are `standard`, `weekly`, `monthly`,`quarterly` and `yearly`.
		 * @type {String}
		 */
		type: {
			default: 'standard',
			schema: {
				type: 'string',
				enum: ['standard', 'weekly', 'monthly', 'quarterly', 'yearly']
			}
		},
		/**
		 * Underlying data for the component.
		 * @type {string}
		 */
		value: {}
	},
	transformState,
	dispatches: {
		/**
		 * Dispatched when a new date is chosen on the calendar.
		 * @type {{value: string}}
		 */
		'NOW_RECORD_MINI_CALENDAR#VALUE_CHANGED': {}
	}
});
