import {
	flatten,
	isFunction,
	isObject,
	isString,
	map,
	range,
	reduce
} from 'lodash';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import moment from 'moment';
import * as Constants from '../constants';

export function handleKeyPress({ keymap, highlightedDate, reversed, event }) {
	const maybeCtrlHandler = event.ctrlKey && keymap[`Ctrl${event.key}`];
	const handler = maybeCtrlHandler || keymap[event.key];

	// Ignore any keys that are not part of the keymap
	if (!isObject(handler)) {
		return highlightedDate;
	}

	event.preventDefault();

	const isReversed = isAttrTrue(reversed);
	const currentDate = highlightedDate.clone();
	return isFunction(handler)
		? handler(currentDate, isReversed)
		: currentDate.add(handler);
}

export function isDateIncluded(date, format, datesToLookFor) {
	const checkDate = moment(date, format);
	return datesToLookFor.reduce((prev, compareRange) => {
		let answer = false;
		if (isString(compareRange)) {
			const compareDate = moment(compareRange, format);
			answer = compareDate.isSame(checkDate);
		} else if (
			isObject(compareRange) &&
			compareRange.start &&
			compareRange.end
		) {
			const start = moment(compareRange.start, format);
			const end = moment(compareRange.end, format);
			answer = checkDate.isSameOrAfter(start) && checkDate.isSameOrBefore(end);
		} else if (typeof compareRange === 'object' && compareRange.before) {
			const beforeDate = moment(compareRange.before, format);
			answer = checkDate.isBefore(beforeDate);
		} else if (typeof compareRange === 'object' && compareRange.after) {
			const afterDate = moment(compareRange.after, format);
			answer = checkDate.isAfter(afterDate);
		}
		return prev || answer;
	}, false);
}

/**
 * Given a date (contains month and year), a format string, and an array of disabled dates,
 * this function returns a set of days for the month that should be disabled
 * @param {string} date - Date string formatted
 * @param {string} format - Date format to parse the dates with
 * @param {Array<string|{start:string, end:string}|{before:string}|{after:string}>} disabledDates - Array of dates that should be disabled
 */
export const getDisabledDays = (date, format, disabledDates) => {
	const monthStart = date.clone().startOf('month');
	const monthEnd = date
		.clone()
		.endOf('month')
		.startOf('day');
	const disabledDays = map(disabledDates, element => {
		if (element) {
			// Single date
			if (isString(element)) {
				const dateElement = moment(element, format);
				return dateElement.isBetween(monthStart, monthEnd, 'day', '[]')
					? dateElement.date()
					: [];
			}
			// Date ranges
			else {
				let { start, end, after, before } = element;
				let dates = [];

				// Start and end range
				if (start && end) {
					start = moment(start, format);
					end = moment(end, format);

					const startInMonth = start.month() === date.month();
					const endInMonth = end.month() === date.month();
					const monthInRange = date.isBetween(start, end, 'month', '[]');

					// We only care if the range touches the month
					if (startInMonth || endInMonth || monthInRange) {
						const maxStart = moment.max(start, monthStart);
						const minEnd = moment.min(end, monthEnd);
						dates = [...range(maxStart.date(), minEnd.date() + 1)];
					}
				}

				// Open ended before range
				if (before) {
					before = moment(before, format).startOf('day');
					// We only care about before if it intersects with the current month
					if (before.isAfter(monthStart)) {
						const endDay = before.isSameOrBefore(monthEnd)
							? before.date()
							: monthEnd.date() + 1;
						dates = [...dates, ...range(monthStart.date(), endDay)];
					}
				}

				// Open ended after range
				if (after) {
					after = moment(after, format).startOf('day');
					// We only care about after if it intersects with the current month
					if (after.isBefore(monthEnd)) {
						const startDay = after.isSameOrAfter(monthStart)
							? after.date() + 1
							: monthStart.date();
						dates = [...dates, ...range(startDay, monthEnd.date() + 1)];
					}
				}

				return dates;
			}
		}
		return [];
	});

	return new Set(flatten(disabledDays));
};

/**
 * Determines expected date format given the calendar type or format properties
 */
export const getFormat = ({ timePicker, hasTimePicker, format, type }) => {
	if (type === Constants.STANDARD_TYPE)
		return (
			format ||
			(timePicker || hasTimePicker ? 'YYYY-MM-DD hh:mm:ss a' : 'YYYY-MM-DD')
		);

	return Constants[`${type.toUpperCase()}_FORMAT`];
};
