import React from 'react';
import { map } from 'lodash';
import makeClass from 'classnames';

export const Week = ({ week, rangePicker, showAdjacentMonthDays }) => {
	return (
		<React.Fragment>
			{map(week, ({ day, disabled, adjacentMonthDay }) => {
				const showDay = disabled
					? !adjacentMonthDay || (adjacentMonthDay && showAdjacentMonthDays)
					: true;
				const className = makeClass(
					'now-record-date-picker-weekly-month-week-day',
					{
						'is-disabled': rangePicker ? disabled : false,
						'is-disabled-date-picker': !rangePicker ? disabled : false,
						'is-adjacent': showAdjacentMonthDays
					}
				);
				return (
					<span key={day} aria-disabled={disabled} className={className}>
						{showDay && day}
					</span>
				);
			})}
		</React.Fragment>
	);
};
