import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-accordion';

import {
	getTimeAgoGroups,
	getTimeAgoByGroup,
	isTimeAgoInGroup,
	getTimeAgoCustom
} from '../utils/timeAgo';

import { PRESET_SELECTED } from './constants';

const TimeAgoButton = ({
	selected,
	timeAgo,
	isCustom = false,
	handleTimeAgoSelect
}) => {
	const label = (timeAgo && timeAgo.label) || '';
	const handleSelection = e => {
		if (!isCustom) {
			handleTimeAgoSelect(timeAgo);
			e.stopPropagation();
		}
	};
	return (
		<div
			class={{
				'date-preset-item': true,
				'is-custom': isCustom
			}}
		>
			<span className="date-preset-item-selected">
				{selected ? (
					<now-icon size="md" aria-hidden="true" icon="check-fill" />
				) : null}
			</span>

			<span
				className="date-preset-item-button"
				aria-label={label}
				role="button"
				{...(isCustom ? { tabindex: '-1' } : { tabIndex: 0 })}
				on-click={e => {
					handleSelection(e);
				}}
				on-keydown={e => {
					if (e.key === 'Enter' || e.key === ' ') {
						handleSelection(e);
					}
				}}
			>
				{label}
			</span>
		</div>
	);
};

const AccordionItems = ({
	presetGroups,
	timeAgoPresets,
	selectedTimeAgo,
	handleTimeAgoSelect
}) => {
	return presetGroups.map(group => {
		const timeAgoGroupItems = getTimeAgoByGroup(timeAgoPresets, group);
		const itemInGroup = selectedTimeAgo
			? isTimeAgoInGroup(timeAgoGroupItems, selectedTimeAgo.label)
			: false;
		return (
			<now-accordion-item
				expanded={itemInGroup}
				header={{
					label: group,
					weight: 'normal',
					size: 'md',
					variant: 'primary'
				}}
				trigger-icon={{ position: 'start' }}
			>
				{itemInGroup ? (
					<now-icon
						className="date-preset-presence"
						slot="metadata"
						icon="presence-fill"
						size="md"
					></now-icon>
				) : null}
				<div slot="content">
					{timeAgoGroupItems.map(timeAgo => (
						<TimeAgoButton
							selected={
								selectedTimeAgo &&
								selectedTimeAgo.label &&
								selectedTimeAgo.label === timeAgo.label
							}
							handleTimeAgoSelect={handleTimeAgoSelect}
							timeAgo={timeAgo}
						/>
					))}
				</div>
			</now-accordion-item>
		);
	});
};

export default (
	{ properties: { hasTime }, selectedTimeAgo, timeAgoPresets },
	{ dispatch, updateState }
) => {
	const handleTimeAgoSelect = timeAgo => {
		updateState({ selectedTimeAgo: timeAgo });
		dispatch(PRESET_SELECTED, { timeAgo });
	};
	const customTimeAgo = getTimeAgoCustom(timeAgoPresets);

	return (
		<div className="date-preset-wrapper">
			<div className="date-preset" id={`date-preset ${timeAgoPresets.length}`}>
				<now-accordion
					expand-single
					hide-dividers
					heading-level="3"
					trigger-icon={{ position: 'start' }}
				>
					<AccordionItems
						presetGroups={getTimeAgoGroups(timeAgoPresets, hasTime)}
						timeAgoPresets={timeAgoPresets}
						selectedTimeAgo={selectedTimeAgo}
						handleTimeAgoSelect={handleTimeAgoSelect}
					/>
				</now-accordion>
				<hr className="date-preset-custom-divider" aria-hidden="true" />
				<TimeAgoButton
					selected={
						selectedTimeAgo &&
						selectedTimeAgo.label &&
						selectedTimeAgo.label === customTimeAgo.label
					}
					handleTimeAgoSelect={handleTimeAgoSelect}
					timeAgo={customTimeAgo}
					isCustom={true}
				/>
			</div>
		</div>
	);
};
