import { map, max, range } from 'lodash';
import { t } from 'sn-translate';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const VISIBLE_YEARS_DELTA = 100;
const VIEW_NEXT_YEAR = t('View next year');
const VIEW_CURRENT_YEAR = t('View current year');
const VIEW_PREV_YEAR = t('View previous year');

export default ({ properties: { year: displayedYear, isRangePicker } }) => {
	return (
		<div className="header">
			{isRangePicker ? (
				<Fragment>
					<div className="left-header">
						<YearSelect selectedYear={displayedYear} id="start" />
					</div>
					<span className="spacer" />
				</Fragment>
			) : null}
			<div className="right-header">
				<YearSelect
					selectedYear={isRangePicker ? displayedYear + 1 : displayedYear}
					id="end"
				/>
				<YearNavigation />
			</div>
		</div>
	);
};

const YearSelect = ({ selectedYear, id }) => {
	const firstYear = max([0, selectedYear - VISIBLE_YEARS_DELTA]);

	const years = map(
		range(firstYear, firstYear + VISIBLE_YEARS_DELTA * 2 + 1),
		label => ({ id: String(label), label: String(label) })
	);

	return (
		<now-dropdown
			bare
			variant="secondary"
			items={years}
			selectedItems={[String(selectedYear)]}
			config-aria={{
				'aria-label': t('Year {0}', selectedYear)
			}}
			append-to-payload={{ id }}
		/>
	);
};

const YearNavigation = () => (
	<div>
		<now-button
			bare
			size="sm"
			variant="tertiary"
			icon="chevron-left-fill"
			tooltipContent={VIEW_PREV_YEAR}
			append-to-payload={{ action: 'previous' }}
		/>
		<now-button
			bare
			size="sm"
			variant="tertiary"
			icon="circle-outline"
			tooltipContent={VIEW_CURRENT_YEAR}
			append-to-payload={{ action: 'current' }}
		/>
		<now-button
			bare
			size="sm"
			variant="tertiary"
			icon="chevron-right-fill"
			tooltipContent={VIEW_NEXT_YEAR}
			append-to-payload={{ action: 'next' }}
		/>
	</div>
);
