import { t } from 'sn-translate';
import { YEARLY_VISIBLE_YEARS } from '../../constants';

const VIEW_NEXT_YEARS = t('View next set of years');
const VIEW_CURRENT_YEAR = t('View current year');
const VIEW_PREV_YEARS = t('View previous set of years');

/**
 * Creates the header portion of the view
 */
export const YearlyHeader = () => {
	return (
		<div className="header">
			<now-button
				bare
				size="sm"
				variant="tertiary"
				icon="chevron-left-fill"
				tooltipContent={VIEW_PREV_YEARS}
				append-to-payload={{ action: -YEARLY_VISIBLE_YEARS }}
			/>
			<now-button
				bare
				size="sm"
				variant="tertiary"
				icon="circle-outline"
				tooltipContent={VIEW_CURRENT_YEAR}
				append-to-payload={{ action: 'current' }}
			/>
			<now-button
				bare
				size="sm"
				variant="tertiary"
				icon="chevron-right-fill"
				tooltipContent={VIEW_NEXT_YEARS}
				append-to-payload={{ action: YEARLY_VISIBLE_YEARS }}
			/>
		</div>
	);
};
