import React from 'react';
import { map } from 'lodash';
import makeClass from 'classnames';
import moment from 'moment';
import { t } from 'sn-translate';
import {
	nextFocusControlElement,
	nextFocusDayElement,
	handleRangePickerDayTab
} from '../../utils/elements';
import { isEditable } from '@devsnc/sn-controls-common';
import { handleKeyPress } from '../../utils/calendarUtils';
import '@devsnc/sn-record-control';
import { getDateRangeRows } from '../../utils/gridUtils';

// Mapping for how various keyboard events affect the selected date.
// If the ctrl modifier matters, prefix key name with `Ctrl`.
//
// Values can either be functions (oldDate => newDate), or objects
// that are passed to moment#add.
const KEYMAP = {
	ArrowLeft: (date, isReversed) => date.add(isReversed ? 1 : -1, 'days'),
	ArrowRight: (date, isReversed) => date.add(isReversed ? -1 : 1, 'days'),
	ArrowUp: { week: -1 },
	ArrowDown: { week: +1 },
	PageUp: { months: -1 },
	CtrlPageUp: { years: -1 },
	PageDown: { months: +1 },
	CtrlPageDown: { years: +1 },
	Home: date => date.startOf('month'),
	End: date => date.endOf('month'),
	Enter: date => date,
	Spacebar: date => date,
	' ': date => date
};

/**
 * Wraps Month Grid to fit nicely within the month view
 */
export class StandardGrid extends React.PureComponent {
	state = {
		date: this.props.date,
		highlightedDate: this.props.highlightedDate,
		hasFocus: false,
		rightMonth: this.props.rightMonth,
		mouseDown: false
	};

	componentWillReceiveProps({ date, highlightedDate }) {
		this.setState({ date, highlightedDate });
	}

	/**
	 * Handle keyboard events. See description for KEYMAP above.
	 */
	onKeyDown = event => {
		if (!isEditable(this.props)) {
			return;
		}

		const nextDate = handleKeyPress({
			event,
			keymap: KEYMAP,
			highlightedDate: this.state.highlightedDate,
			reversed: this.props.reversed
		});

		switch (event.key) {
			case 'Tab':
				if (
					event.currentTarget &&
					event.currentTarget.className.includes(
						'now-record-date-picker-month-wrapper-outer'
					)
				) {
					if (event.shiftKey || (this.props.rightMonth && !event.shiftKey)) {
						const controlToFocus = nextFocusControlElement(
							event.currentTarget,
							this.props.rightMonth
						);
						if (controlToFocus) {
							event.preventDefault();
							event.stopPropagation();
							controlToFocus.focus();
						}
					} else {
						if (!this.props.rightMonth) event.currentTarget.blur();
					}
				}
				break;
			case 'PageUp':
			case 'CtrlPageUp':
			case 'PageDown':
			case 'CtrlPageDown':
			case 'Home':
			case 'End':
			case 'ArrowDown':
			case 'ArrowUp':
			case 'ArrowRight':
			case 'ArrowLeft':
				setTimeout(() => {
					const dayToFocus = nextFocusDayElement(
						this.props.parentRef,
						nextDate
					);
					if (dayToFocus) {
						dayToFocus.tabIndex = '0';
						dayToFocus.focus();
					}
				}, 0);
				break;
		}
		if (nextDate !== this.state.highlightedDate) {
			if (
				event.key === 'Enter' ||
				event.key === ' ' ||
				event.key === 'Spacebar'
			)
				this.props.onSelectDate(nextDate);
			else {
				this.props.onHighlightedDate(nextDate);
				if (this.props.onEndDayHover) this.props.onEndDayHover(nextDate);
			}
		}
	};

	onDayHover = day => {
		const hoverDate = day ? this.props.visibleMonth.clone().date(day) : '';
		if (hoverDate) this.props.onHighlightedDate(moment(hoverDate));
		this.props.onEndDayHover(hoverDate);
	};

	render() {
		return (
			<div
				className="now-record-date-picker-month-wrapper-outer"
				onKeyDown={this.onKeyDown}
				onMouseDown={() => {
					if (!this.state.rightMonth) {
						this.setState({
							mouseDown: true,
							hasFocus: false,
							rightMonth: false
						});
					}
				}}
				onFocus={() => {
					if (!this.state.mouseDown) {
						this.setState({ hasFocus: true });
					}
				}}
				onBlur={() =>
					this.setState({
						hasFocus: false,
						rightMonth: false,
						mouseDown: false
					})
				}
			>
				<div
					className="now-record-date-picker-month-wrapper-inner"
					role="presentation"
					aria-label={t(' Calendar Grid. Use arrow keys to adjust the date.')}
				>
					<MonthGrid
						visibleMonth={this.props.visibleMonth}
						onSelectDate={this.props.onSelectDate}
						onHighlightedDate={this.props.onHighlightedDate}
						rangePicker={this.props.rangePicker}
						rangePickerRef={this.props.rangePickerRef}
						start={this.props.start}
						end={this.props.end}
						format={this.props.format}
						weeklyView={this.props.weeklyView}
						firstDayOfWeek={this.props.firstDayOfWeek}
						showAdjacentMonthDays={this.props.showAdjacentMonthDays}
						onDayHover={this.onDayHover}
						hoverEnd={this.props.hoverEnd}
						{...(this.props.rangePicker &&
						(this.state.hasFocus || this.state.rightMonth)
							? { highlightedDate: this.state.highlightedDate }
							: {})}
						disabledDays={this.props.disabledDays}
						currentDate={this.props.currentDate}
					/>
				</div>
			</div>
		);
	}
}

/**
 * Grid of days for the curent month (plus a few extra days from prev/next
 * month to balance out the calendar
 */
export function MonthGrid({
	date,
	highlightedDate,
	visibleMonth = date,
	start = date,
	end = date,
	format = 'MM-DD-YYYY',
	onSelectDate,
	onHighlightedDate,
	firstDayOfWeek = 0,
	showAdjacentMonthDays,
	onDayHover,
	rangePicker,
	rangePickerRef,
	hoverEnd = '',
	disabledDays,
	currentDate
}) {
	const months = getDateRangeRows(visibleMonth, firstDayOfWeek, disabledDays);
	const normalizeDate = date => date.clone().startOf('day');
	const normalizedStart = normalizeDate(moment(start, format));
	const normalizedEnd = normalizeDate(moment(end, format));
	const normalizedHoverEnd = normalizeDate(moment(hoverEnd, format));
	const today = normalizeDate(moment());
	const none = !start;
	const onlyStart = start && !end;

	return (
		<div className="now-record-date-picker-month">
			{map(months, (week, weekIndex) => (
				<div key={weekIndex} className="now-record-date-picker-month-week">
					{map(week, ({ day, disabled, adjacentMonthDay }) => {
						let normalizedVisibleMonth = normalizeDate(visibleMonth).date(day);

						const isCurrentDate = !rangePicker
							? normalizedVisibleMonth.isSame(normalizeDate(currentDate))
							: onlyStart
							? normalizedVisibleMonth.isSame(normalizedStart)
							: normalizedVisibleMonth.isSame(normalizedStart) ||
							  normalizedVisibleMonth.isSame(normalizedEnd);

						// None selected: Nothing
						// Start selected: Start only
						// Start + end: Start --> End
						const isSelected = none
							? false
							: onlyStart
							? normalizedVisibleMonth.isSame(normalizedStart)
							: normalizedVisibleMonth.isSame(normalizedStart) ||
							  normalizedVisibleMonth.isSame(normalizedEnd);

						const isPreview =
							!isSelected &&
							onlyStart &&
							normalizedVisibleMonth.isAfter(normalizedStart) &&
							normalizedVisibleMonth.isBefore(normalizedHoverEnd);

						const isInsideRange =
							normalizedVisibleMonth.isAfter(normalizedStart) &&
							normalizedVisibleMonth.isBefore(normalizedEnd) &&
							!onlyStart;

						const isHighlighted =
							onHighlightedDate &&
							!isSelected &&
							normalizedVisibleMonth.isSame(highlightedDate);

						const isToday = today.isSame(normalizedVisibleMonth);

						if (disabled) {
							const className = makeClass(
								'now-record-date-picker-month-week-day',
								'is-disabled',
								{
									'is-outlined': !adjacentMonthDay && isToday,
									'is-selected-secondary': !adjacentMonthDay && isInsideRange
								}
							);
							const showDay =
								!adjacentMonthDay ||
								(adjacentMonthDay && showAdjacentMonthDays);

							return (
								<div key={day} aria-disabled="true" className={className}>
									{showDay ? day : ''}
								</div>
							);
						}

						const className = makeClass(
							'now-record-date-picker-month-week-day',
							{
								'is-selected': !isCurrentDate && onSelectDate && isSelected,
								'is-outlined': isToday,
								'is-selected-secondary': isPreview || isInsideRange,
								'hover-secondary-selected': isInsideRange,
								'hover-highlighted': isHighlighted,
								'hover-secondary-highlighted': isInsideRange && isHighlighted,
								'is-current-date': isCurrentDate
							}
						);

						return (
							<div
								key={day}
								aria-disabled="false"
								className={className}
								onClick={() => onSelectDate(visibleMonth.clone().date(day))}
								onMouseEnter={
									onlyStart
										? () => {
												onDayHover(day);
										  }
										: () => {}
								}
								onMouseLeave={
									onlyStart
										? () => {
												onDayHover('');
										  }
										: () => {}
								}
								onKeyDown={event => {
									if (event.key == 'Tab' && rangePicker) {
										handleRangePickerDayTab(rangePickerRef, event);
									}
								}}
								role="button"
								{...(isHighlighted || (isSelected && !rangePicker)
									? { tabIndex: 0 }
									: { tabIndex: -1 })}
								aria-label={visibleMonth
									.clone()
									.date(day)
									.format('LL')}
							>
								<span aria-hidden="true">{day}</span>
							</div>
						);
					})}
				</div>
			))}
		</div>
	);
}
