import moment from 'moment';
import {
	QUARTERLY_FORMAT,
	SET_VISIBLE_YEAR,
	INIT_HIGHLIGHTED_DATE,
	SET_DATE
} from '../../constants';

export default {
	[INIT_HIGHLIGHTED_DATE]: {
		effect: ({
			updateState,
			state: { highlightedQuarter: currentHighlightedQuarter },
			properties: { format = '[Q]Q YYYY', value },
			action: {
				payload: { displayedYear: visibleYear }
			}
		}) => {
			// Set highlighted quarter in this priority order:
			// 1. Existing highlighted
			// 2. Start date
			// 3. Current date
			const highlightedQuarter =
				currentHighlightedQuarter && currentHighlightedQuarter.isValid()
					? currentHighlightedQuarter
					: value
					? moment(value, format)
					: moment();

			const highlightedYear = highlightedQuarter.year();

			// If the date from above is not visible based on the visibleYear, default to first quarter
			const highlightedDateNotVisible = highlightedYear != Number(visibleYear);

			updateState({
				highlightedQuarter: highlightedDateNotVisible
					? moment(`1 ${visibleYear}`, QUARTERLY_FORMAT)
					: highlightedQuarter
			});
		},
		stopPropagation: true
	},
	[SET_VISIBLE_YEAR]: {
		effect: ({
			action: {
				payload: { year }
			},
			dispatch,
			updateState,
			properties: { value, embedded }
		}) => {
			updateState({ visibleYear: year });

			//for mini calendar (embedded will be true) consider year change as selection
			//for date-picker embedded will be false
			if (embedded) {
				const date = moment(value, QUARTERLY_FORMAT).set('year', year);
				dispatch(SET_DATE, { date });
			}
		},
		stopPropagation: true
	},
	[SET_DATE]: {
		effect: ({ action, properties: { onSelectDate, value: propsValue } }) => {
			const value = action.payload.date;

			const formattedValue = value.format(QUARTERLY_FORMAT);
			if (propsValue !== formattedValue) {
				onSelectDate(action.payload.date);
			}
		},
		stopPropagation: true
	}
};
