import React from 'react';
import { t } from 'sn-translate';

import { time, isEditable } from '@devsnc/sn-controls-common';
import '@devsnc/sn-record-control';

import {
	nextFocusControlElement,
	nextFocusWeekElement,
	handleRangePickerWeekTab
} from '../../utils/elements';
import { handleKeyPress } from '../../utils/calendarUtils';

import { Weeks } from './Weeks';

const KEYMAP = {
	ArrowLeft: (date, isReversed) => date.add(isReversed ? 1 : -1, 'months'),
	ArrowRight: (date, isReversed) => date.add(isReversed ? -1 : 1, 'months'),
	ArrowUp: { week: -1 },
	ArrowDown: { week: +1 },
	PageUp: { months: -1 },
	PageDown: { months: +1 },
	CtrlPageUp: { years: -1 },
	CtrlPageDown: { years: +1 },
	Home: date => date.startOf('month'),
	End: date => date.endOf('month'),
	Enter: date => date,
	Spacebar: date => date,
	' ': date => date
};

/**
 * Wraps Week Grid to fit nicely within the month view
 */
export class WeeklyGrid extends React.PureComponent {
	state = {
		date: this.props.date,
		highlightedDate: this.props.highlightedDate,
		hasFocus: false,
		rightMonth: this.props.rightMonth,
		mouseDown: false
	};

	componentWillReceiveProps({ date, highlightedDate }) {
		this.setState({ date });
		if (highlightedDate && highlightedDate.isValid()) {
			this.setState({ highlightedDate });
		}
	}

	/**
	 * Handle keyboard events. See description for KEYMAP above.
	 */
	onKeyDown = event => {
		if (!isEditable(this.props)) {
			return;
		}

		const weekCurrent = event.target.id;
		const weekCurrentDay = time(this.props.language)(
			weekCurrent,
			this.props.format
		);

		const nextDate = handleKeyPress({
			event,
			keymap: KEYMAP,
			highlightedDate: weekCurrentDay,
			reversed: this.props.reversed
		});

		if (
			nextDate !== this.state.highlightedDate &&
			event.key !== 'Tab' &&
			!event.shiftKey
		) {
			if (
				event.key === 'Enter' ||
				event.key === ' ' ||
				event.key === 'Spacebar'
			)
				this.props.onSelectDate(nextDate);
			else {
				this.props.onHighlightedDate(nextDate);
				if (this.props.onEndDayHover) this.props.onEndDayHover(nextDate);
			}
		}

		switch (event.key) {
			case 'Tab':
				if (
					this.props.rangePicker &&
					event.currentTarget &&
					event.currentTarget.className.includes(
						'now-record-date-picker-weekly-month-week-row'
					)
				) {
					handleRangePickerWeekTab(this.props.rangePickerRef, event);
				}
				if (
					event.currentTarget &&
					event.currentTarget.className.includes(
						'now-record-date-picker-month-wrapper-outer'
					)
				) {
					if (event.shiftKey || (this.props.rightMonth && !event.shiftKey)) {
						const controlToFocus = nextFocusControlElement(
							event.currentTarget,
							this.props.rightMonth
						);
						if (controlToFocus) {
							event.preventDefault();
							event.stopPropagation();
							controlToFocus.focus();
						}
					} else {
						if (!this.props.rightMonth) event.currentTarget.blur();
					}
				}
				break;
			case 'PageUp':
			case 'CtrlPageUp':
			case 'PageDown':
			case 'CtrlPageDown':
			case 'Home':
			case 'End':
			case 'ArrowDown':
			case 'ArrowUp':
			case 'ArrowRight':
			case 'ArrowLeft':
				setTimeout(() => {
					const weekToFocus = nextFocusWeekElement(
						nextDate,
						this.props.rangePickerRef,
						this.props.parentRef
					);
					if (weekToFocus) {
						weekToFocus.tabIndex = '0';
						weekToFocus.focus();
					}
					this.setState({ highlightedDate: nextDate });
				}, 0);
				break;
		}
	};

	render() {
		return (
			<div
				className="now-record-date-picker-month-wrapper-outer"
				onKeyDown={this.onKeyDown}
				onMouseDown={() => {
					if (!this.state.rightMonth) {
						this.setState({
							mouseDown: true,
							hasFocus: false,
							rightMonth: false
						});
					}
				}}
				onFocus={() => {
					if (!this.state.mouseDown) {
						this.setState({ hasFocus: true });
					}
				}}
				onBlur={() =>
					this.setState({
						hasFocus: false,
						rightMonth: false,
						mouseDown: false
					})
				}
			>
				<div
					className="now-record-date-picker-month-wrapper-inner"
					role="presentation"
					aria-label={t(' Calendar Grid. Use arrow keys to adjust the date.')}
				>
					<Weeks
						date={this.state.date}
						visibleMonth={this.props.visibleMonth}
						start={this.props.start}
						end={this.props.end}
						format={this.props.format}
						onSelectDate={this.props.onSelectDate}
						onEndDayHover={this.props.onEndDayHover}
						onHighlightedDate={this.props.onHighlightedDate}
						firstDayOfWeek={this.props.firstDayOfWeek}
						showAdjacentMonthDays={
							this.props.rangePicker ? this.props.showAdjacentMonthDays : true
						}
						rangePicker={this.props.rangePicker}
						language={this.props.language}
						hoverEnd={this.props.hoverEnd}
						{...(this.props.rangePicker &&
						(this.state.hasFocus || this.state.rightMonth)
							? { highlightedDate: this.state.highlightedDate }
							: {})}
						disabledDays={this.props.disabledDays}
						rangePickerRef={this.props.rangePickerRef}
						currentDate={this.props.currentDate}
					/>
				</div>
			</div>
		);
	}
}
