import React from 'react';
import { range, map, max } from 'lodash';
import { t } from 'sn-translate';
import { handleRangePickerYearTab } from '../../utils/elements';
import '@devsnc/sn-record-control';
import '../../month-header';

const VISIBLE_YEARS_DELTA = 100;

/**
 * Heading display with controls to toggle year view and displayed month
 */
export function YearLabel({
	date,
	onChangeMonth,
	disabled,
	reversed,
	enablePreviousMonthControl = true,
	enableNextMonthControl = true,
	highlightedDate,
	rangePicker,
	rangePickerRef,
	weeklyView,
	enableMonthNavigationChevrons = true
}) {
	const month = date.format('MMMM');
	const firstYear = max([
		0,
		date
			.clone()
			.subtract(VISIBLE_YEARS_DELTA, 'years')
			.year()
	]);
	const years = map(
		range(firstYear, firstYear + VISIBLE_YEARS_DELTA * 2 + 1),
		label => ({ id: label + '', label: label + '' })
	);

	return (
		<div className="now-record-date-picker-header">
			<div className="now-record-date-picker-content">
				<div
					className="now-record-date-picker-header-label"
					onKeyDown={event => {
						if (
							event.key == 'Tab' &&
							rangePicker &&
							rangePickerRef &&
							rangePickerRef.current
						) {
							handleRangePickerYearTab(
								rangePickerRef,
								event,
								highlightedDate,
								weeklyView,
								enableMonthNavigationChevrons
							);
						}
					}}
				>
					<span>{month}</span>
					<sn-record-dropdown-internal
						bare
						on-change={({ value }) => onChangeMonth(date.clone().year(value))}
						selected-items={[date.format('YYYY')]}
						variant="secondary"
						size="md"
						items={years}
						config-aria={{ 'aria-label': t('Year {0}', date.year()) }}
					/>
				</div>
				{enableMonthNavigationChevrons && (
					<sn-month-header
						date={date}
						onChangeMonth={onChangeMonth}
						enablePreviousMonthControl={enablePreviousMonthControl}
						enableNextMonthControl={enableNextMonthControl}
						reversed={reversed}
						disabled={disabled}
						rangePicker={rangePicker}
						rangePickerRef={rangePickerRef}
						weeklyView={weeklyView}
						highlightedDate={highlightedDate}
					/>
				)}
			</div>
		</div>
	);
}
