export function getTimeAgoGroups(timeAgoPresets, hasTime) {
	return uniqBy(timeAgoPresets, 'group')
		.map(timeAgo => timeAgo.group)
		.filter(group => group !== CUSTOM_PRESET_LABEL)
		.filter(group => (hasTime ? true : !TIME_PRESET_GROUPS.includes(group)));
}

export function getTimeAgoByGroup(timeAgoPresets, group) {
	return timeAgoPresets.filter(timeAgo => timeAgo.group === group);
}

export function getTimeAgoByLabel(timeAgoPresets, label) {
	return find(timeAgoPresets, timeAgo => timeAgo.label === label);
}

export function isTimeAgoInGroup(timeAgoPresets, label) {
	return timeAgoPresets
		.filter(group => group !== CUSTOM_PRESET_LABEL)
		.some(timeAgo => timeAgo.label === label);
}

export function getTimeAgoCustom(timeAgoPresets) {
	return getTimeAgoByLabel(timeAgoPresets, CUSTOM_PRESET_LABEL);
}

export const getTimeAgoByValue = (timeAgoPresets, format, value) => {
	const absoluteDate = moment(value, format, true);
	if (absoluteDate.isValid()) {
		return getTimeAgoCustom(timeAgoPresets);
	} else {
		return getTimeAgoByLabel(timeAgoPresets, value);
	}
};
