import moment from 'moment';

import { QUARTERLY_FORMAT, INVALID_MOMENT } from '../../constants';
import { QuarterlyGrid } from '../../grids/quarterly/QuarterlyGrid';

import BottomBar from '../../bottom-bar';
import '../../year-header';

export default (
	{
		highlightedQuarter = INVALID_MOMENT,
		hoveredQuarter = INVALID_MOMENT,
		visibleYear,
		properties: { value, bottomBar, onOk, onCancel }
	},
	{ dispatch, updateState }
) => {
	if (!visibleYear) {
		visibleYear = value ? moment(value, QUARTERLY_FORMAT) : moment();
		updateState({ visibleYear: visibleYear.year() });
	}

	return (
		<div className="quarterly-view">
			<sn-year-header year={visibleYear} isRangePicker={false} />
			<QuarterlyGrid
				dispatch={dispatch}
				updateState={updateState}
				highlightedQuarter={highlightedQuarter}
				hoveredQuarter={hoveredQuarter}
				displayedYear={visibleYear}
				start={moment(value, QUARTERLY_FORMAT)}
				isRangePicker={false}
			/>
			{bottomBar ? <BottomBar onOk={onOk} onCancel={onCancel} /> : null}
		</div>
	);
};
