import moment from 'moment';

import { SET_VISIBLE_YEAR } from '../constants';

export default {
	'NOW_DROPDOWN#ITEM_CLICKED': {
		effect: ({
			dispatch,
			action: {
				payload: { id, item }
			},
			properties: { isRangePicker }
		}) => {
			const nextYear =
				id === 'start' ? item.label : String(Number(item.label) - 1);

			dispatch(SET_VISIBLE_YEAR, {
				year: Number(isRangePicker ? nextYear : item.label)
			});
		},
		stopPropagation: true
	},
	'NOW_BUTTON#CLICKED': {
		effect: ({
			dispatch,
			action: {
				payload: { action }
			},
			properties: { year }
		}) => {
			let newYear;

			if (action === 'previous') {
				newYear = Number(year) - 1;
			} else if (action === 'next') {
				newYear = Number(year) + 1;
			} else {
				const today = moment();
				newYear = today.year();
			}

			dispatch(SET_VISIBLE_YEAR, {
				year: newYear
			});
		},
		stopPropagation: true
	}
};
