import React from 'react';
import { isNil, noop, isEmpty } from 'lodash';
import { t } from 'sn-translate';

import { time, isEditable, checkAutofocus } from '@devsnc/sn-controls-common';

import { getDisabledDays } from '../../utils/calendarUtils';
import { GridWrapper } from './GridWrapper';
import { WEEKLY_TYPE } from '../../constants';

/**
 * This component (and all sub-components) are _not_ seismic components, so
 * they finally get to pass around moment date objects instead of strings
 */
class CalendarBody extends React.PureComponent {
	constructor(props) {
		super(props);

		const { language, value, format, utcOffset } = this.props;

		const date = time(language)(value, format, true);

		const now = isNil(utcOffset)
			? time(language)()
			: time(language)().utcOffset(utcOffset);
		const initialDate = date.isValid() ? date : now;

		// when date is invalid, set the pending value to the current time
		if (!date.isValid() && !this.props.timeAgo && isEmpty(value)) {
			this.props.onDateChange(initialDate);
		}

		this.state = {
			date: initialDate,
			visibleMonth: initialDate,
			currentDate: initialDate
		};
	}

	onSelectDate = date => {
		if (!date.isValid() || !isEditable(this.props)) {
			return;
		}

		this.setState({
			currentDate: date,
			date,
			visibleMonth: date
		});

		this.props.onDateChange(date);
	};

	onChangeMonth = (date, action, day) => {
		this.setState({ visibleMonth: date });

		//handle view today butto
		if (action && action === 'current' && day) {
			date = date.clone().date(day);
		}

		this.setState({ date });

		//for mini calendar (embedded will be true) consider year/month change as selecting a date
		//for date-picker embedded will be false
		//skip for view today button
		if (this.props.embedded) {
			const disabledDatesForMonth = getDisabledDays(
				date,
				this.props.format,
				this.props.disabledDates
			);
			if (disabledDatesForMonth && disabledDatesForMonth.size) {
				//select date if the date is not a disabled date
				const daySelected = date.date();
				const isADisabledDate = [...disabledDatesForMonth].includes(
					daySelected
				);
				if (!isADisabledDate) {
					this.onSelectDate(date);
				}
			} else {
				//no disabled dates for this month
				this.onSelectDate(date);
			}
		}
	};

	componentWillReceiveProps(nextProps) {
		if (
			nextProps.value !== this.props.value ||
			nextProps.language !== this.props.language
		) {
			const date = time(nextProps.language)(
				nextProps.value,
				nextProps.format,
				true
			);
			if (date.isValid()) {
				this.setState({
					date,
					visibleMonth: date
				});
			} else {
				const { language, utcOffset } = this.props;
				const now = isNil(utcOffset)
					? time(language)()
					: time(language)().utcOffset(utcOffset);
				this.setState({
					date: now,
					visibleMonth: now
				});
			}
		}
	}

	componentDidMount() {
		checkAutofocus(this.props.autofocus, this.rootNode);
	}

	render() {
		return (
			<div
				ref={node => (this.rootNode = node)}
				aria-label={t(
					'Selected date: {0}. Use arrow keys to adjust the selected date.',
					this.state.date.format(this.props.format)
				)}
				role="application"
			>
				<div className="sn-popover-body">
					<GridWrapper
						type={this.props.type}
						disabled={!isEditable(this.props)}
						disabledDays={getDisabledDays(
							this.state.visibleMonth,
							this.props.format,
							this.props.disabledDates
						)}
						date={this.state.date}
						visibleMonth={this.state.visibleMonth}
						ariaLabel={t(
							'Selected date: {0}.',
							this.state.date.format(this.props.format)
						)}
						firstDayOfWeek={this.props.firstDayOfWeek}
						onSelectDate={this.onSelectDate}
						onChangeMonth={this.onChangeMonth}
						reversed={this.props.reversed}
						format={this.props.format}
						weeklyView={this.props.type === WEEKLY_TYPE}
						language={this.props.language}
						highlightedDate={this.state.date}
						onHighlightedDate={newDate => {
							this.setState({ date: newDate, visibleMonth: newDate });
						}}
						currentDate={this.state.currentDate}
					/>
				</div>
			</div>
		);
	}
}

CalendarBody.defaultProps = {
	onDateChange: noop,
	language: '',
	embedded: false
};

export default CalendarBody;
