import { createCustomElement } from '@servicenow/ui-core';

import view from './view';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';

createCustomElement('sn-month-header', {
	view,
	styles,
	properties: {
		date: {
			schema: { type: 'object' }
		},
		onChangeMonth: {
			schema: { type: 'function' }
		},
		enablePreviousMonthControl: {
			schema: { type: 'boolean' }
		},
		enableNextMonthControl: {
			schema: { type: 'boolean' }
		},
		disabled: {
			schema: { type: 'boolean' }
		},
		reversed: {
			schema: { type: 'boolean' }
		},
		rangePicker: {
			schema: { type: 'boolean' }
		},
		rangePickerRef: {
			schema: { type: 'object' }
		},
		highlightedDate: {
			schema: { type: 'object' }
		},
		weeklyView: {
			schema: { type: 'boolean' }
		}
	},
	actionHandlers
});
