import { createCustomElement } from '@servicenow/ui-core';

import view from './quarterlyView';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';
import { INVALID_MOMENT } from '../../constants';

// Ensure QUARTERLY_VIEW_TAG in src/constants matches elementName below
createCustomElement('sn-controls-quarterly-view', {
	view,
	styles,
	properties: {
		value: {
			schema: { type: 'string' }
		},
		onSelectDate: {
			schema: { type: 'function' }
		},
		isRangePicker: {
			schema: { type: 'boolean' }
		},
		bottomBar: {
			schema: { type: 'boolean' }
		},
		onOk: {
			schema: { type: 'function' }
		},
		onCancel: {
			schema: { type: 'function' }
		},
		embedded: {
			schema: { type: 'boolean' },
			default: false
		}
	},
	initialState: {
		highlightedQuarter: INVALID_MOMENT,
		hoveredQuarter: INVALID_MOMENT
	},
	actionHandlers,
	eventHandlers: [
		{
			events: ['blur'],
			effect: ({ updateState }) => {
				updateState({ highlightedQuarter: INVALID_MOMENT });
			}
		}
	]
});
