import { createCustomElement } from '@servicenow/ui-core';

import view from './YearlyView';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';
import { INVALID_MOMENT } from '../../constants';

/**
 * Lets export some common yearly view items so that other components can access them easier
 */
import { YearlyGrid } from '../../grids/yearly/YearlyGrid';
import { YearlyHeader } from './YearlyHeader';
export const Yearly = {
	components: { YearlyHeader, YearlyGrid }
};

// Ensure YEARLY_VIEW_TAG in src/constants matches elementName below
createCustomElement('sn-controls-yearly-view', {
	view,
	styles,
	properties: {
		value: {
			schema: { type: 'string' }
		},
		onSelectDate: {
			schema: { type: 'function' }
		},
		bottomBar: {
			schema: { type: 'boolean' }
		},
		onOk: {
			schema: { type: 'function' }
		},
		onCancel: {
			schema: { type: 'function' }
		},
		embedded: {
			schema: { type: 'boolean' },
			default: false
		}
	},
	initialState: {
		highlightedYear: INVALID_MOMENT,
		hoveredYear: INVALID_MOMENT
	},
	actionHandlers,
	eventHandlers: [
		{
			events: ['blur'],
			effect: ({ updateState }) => {
				updateState({ highlightedYear: INVALID_MOMENT });
			}
		}
	]
});
