import React from 'react';
import moment from 'moment';

import { isEditable } from '@devsnc/sn-controls-common';
import { getFormat } from './utils/calendarUtils';

import { STANDARD_TYPE, STANDARD_VIEW_TAG, WEEKLY_TYPE } from './constants';

import './views/standard/StandardView';
import './views/quarterly';
import './views/yearly';
import './views/monthly';

export class CalendarView extends React.PureComponent {
	static componentName = 'sn-calendar-view';

	render() {
		const { type } = this.props;

		if (type === WEEKLY_TYPE || type === STANDARD_TYPE)
			return <STANDARD_VIEW_TAG {...this.props} />;
		else return <SeismicView {...this.props} />;
	}
}

CalendarView.defaultProps = {
	type: STANDARD_TYPE,
	embedded: false
};

/**
 * Handles all the new seismic views (monthly, yearly, quarterly, etc)
 *
 * This wrapper for seismic views is required so that we can set react states and
 * in general, enter react world (old implementation).
 *
 * This is most important for the current implementation of date-picker where
 * the state of the popup (opened/closed) after selecting a date is determined
 * by whether there was a value stored in the state to begin with. As such,
 * we have to intercept onSelectDate to save the value to the state.
 *
 * TODO: Remove this coupling (see above)
 */
class SeismicView extends React.PureComponent {
	componentDidUpdate = ({ opened: prevOpened }) => {
		let { value, opened } = this.props;

		if (prevOpened !== opened && opened && !value) {
			value = moment();
			this.onDateChange(value, false);
		}
	};

	onSelectDate = date => {
		if (date.isValid() && isEditable(this.props)) {
			this.setState({ date });
			this.onDateChange(date);
		}
	};

	onDateChange = (date, updatePopover) => {
		this.props.onDateChange(
			date.format(getFormat(this.props)),
			null,
			updatePopover
		);
	};

	render() {
		const { type, opened } = this.props;
		const hasValue =
			!!this.props.value &&
			moment(this.props.value, getFormat(this.props), true).isValid();
		const value = hasValue
			? this.props.value
			: moment().format(getFormat(this.props).toString());

		if (opened) {
			const Tag = `sn-controls-${type}-view`;

			return (
				<Tag {...this.props} value={value} onSelectDate={this.onSelectDate} />
			);
		}
		return null;
	}
}
