import { t } from 'sn-translate';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import { handleRangePickerMonthTab } from '../utils/elements';

const VIEW_NEXT_MONTH = t('View next month');
const VIEW_CURRENT_MONTH = t('View Today');
const VIEW_PREV_MONTH = t('View previous month');

export default ({
	properties: {
		enablePreviousMonthControl = true,
		enableNextMonthControl = true,
		disabled,
		reversed,
		rangePicker,
		rangePickerRef,
		weeklyView,
		highlightedDate
	}
}) => {
	return (
		<div className="header">
			{enablePreviousMonthControl ? (
				<now-button
					bare
					size="sm"
					variant="tertiary"
					icon={reversed ? 'chevron-right-fill' : 'chevron-left-fill'}
					tooltipContent={VIEW_PREV_MONTH}
					append-to-payload={{ action: 'previous' }}
					disabled={isAttrTrue(disabled) ? true : undefined}
				/>
			) : null}
			<now-button
				bare
				size="sm"
				variant="tertiary"
				icon="circle-outline"
				tooltipContent={VIEW_CURRENT_MONTH}
				append-to-payload={{ action: 'current' }}
			/>
			<div
				className="now-right-chevron -static"
				on-keydown={event => {
					if (
						event.key == 'Tab' &&
						!event.shiftKey &&
						rangePicker &&
						rangePickerRef &&
						rangePickerRef.current
					) {
						handleRangePickerMonthTab(
							rangePickerRef,
							highlightedDate,
							event,
							weeklyView
						);
					}
				}}
			>
				{enableNextMonthControl ? (
					<now-button
						bare
						size="sm"
						variant="tertiary"
						icon={reversed ? 'chevron-left-fill' : 'chevron-right-fill'}
						icon="chevron-right-fill"
						tooltipContent={VIEW_NEXT_MONTH}
						append-to-payload={{ action: 'next' }}
						disabled={isAttrTrue(disabled) ? true : undefined}
					/>
				) : null}
			</div>
		</div>
	);
};
