import React from 'react';
import { chunk, range, map } from 'lodash';
import { weekdays, mod, cycle } from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';

export function getDateRangeRows(
	date,
	firstDayOfWeek = 0,
	disabledDays = new Set()
) {
	const grid = getDateRange(date, firstDayOfWeek);

	return chunk(
		[
			...map(grid.prevMonth, day => ({
				day,
				disabled: true,
				adjacentMonthDay: true
			})),
			...map(grid.thisMonth, day => ({ day, disabled: disabledDays.has(day) })),
			...map(grid.nextMonth, day => ({
				day,
				disabled: true,
				adjacentMonthDay: true
			}))
		],
		7
	);
}

/**
 * Calculates the necessary days needed to fill a calendar, by taking the days
 * of the current month and padding with data from the previous and next month
 * in order to make a pretty grid.
 */
export function getDateRange(date, firstDayOfWeek = 0) {
	const firstOfThisMonth = date
		.clone()
		.date(1)
		.day();
	// Figure out the number of days we need to use from the last month so that
	// the first row/week is completely filled with days and all aligned
	// properly.
	let daysNeededLastMonth = mod(firstOfThisMonth - firstDayOfWeek, 7);
	daysNeededLastMonth = daysNeededLastMonth === 0 ? 7 : daysNeededLastMonth;

	// Take the number of days that we're rendering so far (some from previous
	// month + number of days of current month). Add enough days from the next
	// month so that we have 6 weeks visible (42 days)
	const daysThisMonth = date.daysInMonth();
	const daysNeededNextMonth = 42 - daysNeededLastMonth - daysThisMonth;

	// Return this data as separate values in an object because we're going to
	// want to style prevMonth and nextMonth values differently than values for
	// the current month.
	const lastDayLastMonth = date
		.clone()
		.subtract(1, 'month')
		.daysInMonth();
	return {
		prevMonth: range(
			lastDayLastMonth - daysNeededLastMonth + 1,
			lastDayLastMonth + 1
		),
		thisMonth: range(1, daysThisMonth + 1),
		nextMonth: range(1, daysNeededNextMonth + 1)
	};
}

/**
 * Display the first letter for each day of the week
 * TODO: i18n
 */
export const DaysOfWeek = ({ firstDayOfWeek = 0, language, weeklyView }) => (
	<div className="now-record-date-picker-week">
		{weeklyView && (
			<div aria-hidden className="now-record-date-picker-week-day -weekly">
				{t('WK')}
			</div>
		)}
		{cycle(weekdays(language), -firstDayOfWeek).map(day => {
			return (
				<div
					aria-hidden
					className={
						'now-record-date-picker-week-day' + (weeklyView ? ' -weekly' : '')
					}
					key={day}
				>
					{day[0].toUpperCase()}
				</div>
			);
		})}
	</div>
);
