import moment from 'moment';

import { MonthlyGrid } from '../../grids/monthly/MonthlyGrid';
import { MONTHLY_FORMAT, INVALID_MOMENT } from '../../constants';

import BottomBar from '../../bottom-bar';
import '../../year-header';

export default (
	{
		highlightedMonth = INVALID_MOMENT,
		hoveredMonth = INVALID_MOMENT,
		visibleYear,
		properties: { value, bottomBar, onOk, onCancel }
	},
	{ dispatch, updateState }
) => {
	let displayedYear = visibleYear;

	if (!displayedYear) {
		displayedYear = value ? moment(value, MONTHLY_FORMAT) : moment();
		visibleYear = displayedYear.year();
		updateState({ visibleYear });
	}

	return (
		<div className="monthly-view">
			<sn-year-header year={visibleYear} isRangePicker={false} />
			<MonthlyGrid
				dispatch={dispatch}
				updateState={updateState}
				highlightedMonth={highlightedMonth}
				hoveredMonth={hoveredMonth}
				displayedYear={displayedYear}
				start={moment(value, MONTHLY_FORMAT)}
			/>
			{bottomBar ? <BottomBar onOk={onOk} onCancel={onCancel} /> : null}
		</div>
	);
};
