import moment from 'moment';

export default {
	'NOW_BUTTON#CLICKED': {
		effect: ({
			action: {
				payload: { action }
			},
			properties: { date, onChangeMonth }
		}) => {
			let updatedDate;

			if (action === 'previous') {
				updatedDate = date.clone().subtract(1, 'months');
			} else if (action === 'next') {
				updatedDate = date.clone().add(1, 'months');
			} else {
				updatedDate = moment();
			}
			if (onChangeMonth) {
				onChangeMonth(updatedDate, action, date.date());
			}
		},
		stopPropagation: true
	}
};
