import moment from 'moment';

import { chunk, map, noop, some } from 'lodash';
import { t } from 'sn-translate';

import { time, monthsShort } from '@devsnc/sn-controls-common';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

import {
	MONTHLY_FORMAT,
	AFFIRMATIVE_KEYS,
	NAVIGATION_KEYS,
	INIT_HIGHLIGHTED_DATE,
	SET_VISIBLE_YEAR,
	SET_DATE
} from '../../constants';

const MONTHS_PER_ROW = 4;

export const MonthlyGrid = ({
	dispatch,
	updateState,
	highlightedMonth,
	hoveredMonth,
	displayedYear,
	language,
	start,
	end,
	isRangePicker
}) => {
	let isFocusedFromClick = false;
	const months = monthsShort(language);
	const monthRows = chunk(months, MONTHS_PER_ROW);

	const handleMonthSelection = (month, year) => () => {
		const date = time(language)({ year, month });
		dispatch(SET_DATE, { date });
		updateState({ highlightedMonth: date });
	};

	const resetHighlighted = () => {
		// reset back to start date or current date
		updateState({ hoveredMonth: null });
	};

	// Calculate Selected + in-between highlighted
	const getMonthGrid = ({ year, selected = [] }) => {
		return monthRows.map((row, rowIndex) => {
			return (
				<div className="month-row">
					{map(row, (month, index) => {
						const monthIndex = index + rowIndex * MONTHS_PER_ROW;

						const isYearAndMonthSame = s =>
							!!s &&
							s.isValid() &&
							s.year() === year &&
							s.month() === monthIndex;

						const isSelected = some(selected, isYearAndMonthSame);

						const isHighlighted = isYearAndMonthSame(highlightedMonth);

						const monthBeingConsidered = moment({ year, month: monthIndex });

						const [start, end] = selected;
						const isInRange =
							end &&
							(monthBeingConsidered.isBetween(start, end) ||
								// don't highlight when end of range is selected
								(!end.isValid() &&
									monthBeingConsidered.isBetween(
										start,
										hoveredMonth || highlightedMonth
									)));

						const isCurrentMonth = isYearAndMonthSame(moment());

						const handleSelection = !isSelected
							? handleMonthSelection(monthIndex, year)
							: noop;

						const handleKeyDown = event => {
							if (AFFIRMATIVE_KEYS.includes(event.key)) {
								handleSelection();
							}
						};

						const updateHighlighted = () => {
							updateState({
								hoveredMonth: moment({ month: monthIndex, year })
							});
						};

						return (
							<span
								id={`${monthIndex}-${year}`}
								class={{
									'month-cell': true,
									'is-current-month': isCurrentMonth,
									'is-selected': isSelected,
									'is-highlighted': isHighlighted,
									'is-in-range': isInRange
								}}
								on-click={handleSelection}
								on-mousedown={() => {
									isFocusedFromClick = true;
								}}
								on-keydown={handleKeyDown}
								on-mouseenter={updateHighlighted}
								on-mouseleave={resetHighlighted}
								role="button"
								aria-label={`${month} ${year}`}
							>
								{month}
							</span>
						);
					})}
				</div>
			);
		});
	};

	const leftGrid = (
		<div className="month-grid">
			{getMonthGrid({
				year: displayedYear,
				selected: [start, end]
			})}
		</div>
	);

	const rightGrid = isRangePicker ? (
		<Fragment>
			<span className="spacer" />
			<div className="month-grid">
				{getMonthGrid({
					year: displayedYear + 1,
					selected: [start, end]
				})}
			</div>
		</Fragment>
	) : null;

	const handleNavigation = e => {
		/*
		state:
			highlightedDate: start | first_cell

		0. On navigation, update `highlightedDate`
		1a. On enter:
			start/end set to highlightedDate
		1b. On click:
			start/end set to selected, highlighteDate set to selected
		*/

		const { key } = e;

		const getNextHighlightedMonth = key => {
			let operation, increment;
			switch (key) {
				case 'ArrowRight':
					operation = 'add';
					increment = 1;
					break;
				case 'ArrowLeft':
					operation = 'subtract';
					increment = 1;
					break;
				case 'ArrowDown':
					operation = 'add';
					increment = 4;
					break;
				case 'ArrowUp':
					operation = 'subtract';
					increment = 4;
					break;
				default:
					return highlightedMonth;
			}

			return highlightedMonth.clone()[operation](increment, 'month');
		};

		if (NAVIGATION_KEYS.includes(key)) {
			const nextHighlightedMonth = getNextHighlightedMonth(key);

			updateState({
				highlightedMonth: nextHighlightedMonth
			});

			// If the next highlighted date is going prior to the left-side month,
			// go back an additional year so it ends up wrapping rather than jumping
			if (nextHighlightedMonth.year() < displayedYear) {
				dispatch(SET_VISIBLE_YEAR, {
					year: nextHighlightedMonth.year() - (isRangePicker ? 1 : 0)
				});
			} else if (
				nextHighlightedMonth.year() !== displayedYear &&
				(!isRangePicker || nextHighlightedMonth.year() !== displayedYear + 1)
			) {
				dispatch(SET_VISIBLE_YEAR, {
					year: nextHighlightedMonth.year()
				});
			}

			e.preventDefault();

			return;
		}

		if (AFFIRMATIVE_KEYS.includes(key)) {
			e.preventDefault();
			dispatch(SET_DATE, { date: highlightedMonth });
		}
	};

	return (
		<div
			className="monthly-view-grid"
			tabindex="0"
			on-focus={() => {
				// We only want to dispatch this event when we focus to the grid
				// via keyboard navigation. If user clicks an entry in the grid,
				// that will receive the "focus" and we don't want to cause a flicker
				if (!isFocusedFromClick) {
					dispatch(INIT_HIGHLIGHTED_DATE);
				}
				isFocusedFromClick = false;
			}}
			on-keydown={handleNavigation}
			on-mouseleave={resetHighlighted}
			role="application"
			aria-activedescendant={
				highlightedMonth.isValid()
					? `${highlightedMonth.month()}-${highlightedMonth.year()}`
					: undefined
			}
			aria-label={
				highlightedMonth.isValid()
					? t(
							'Selected date: {0}. Use arrow keys to adjust the selected date.',
							highlightedMonth.format(MONTHLY_FORMAT)
					  )
					: undefined
			}
		>
			{leftGrid}
			{rightGrid}
		</div>
	);
};
