import { createCustomElement } from '@servicenow/ui-core';

import view from './MonthlyView';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';

const INVALID_MOMENT = { isValid: () => false };

// Ensure MONTHLY_VIEW_TAG in src/constants matches elementName below
createCustomElement('sn-controls-monthly-view', {
	view,
	styles,
	properties: {
		value: {
			schema: { type: 'string' }
		},
		onSelectDate: {
			schema: { type: 'function' }
		},
		isRangePicker: {
			schema: { type: 'boolean' }
		},
		bottomBar: {
			schema: { type: 'boolean' }
		},
		onOk: {
			schema: { type: 'function' }
		},
		onCancel: {
			schema: { type: 'function' }
		},
		embedded: {
			schema: { type: 'boolean' },
			default: false
		}
	},
	initialState: {
		highlightedMonth: INVALID_MOMENT,
		hoveredMonth: INVALID_MOMENT
	},
	actionHandlers,
	eventHandlers: [
		{
			events: ['blur'],
			effect: ({ updateState }) => {
				updateState({ highlightedMonth: INVALID_MOMENT });
			}
		}
	]
});
