import { inRange } from 'lodash';
import moment from 'moment';

import {
	YEARLY_FORMAT,
	SET_VISIBLE_YEAR,
	INIT_HIGHLIGHTED_DATE,
	SET_DATE
} from '../../constants';

export default {
	[INIT_HIGHLIGHTED_DATE]: {
		effect: ({
			updateState,
			state: { highlightedYear: currentHighlightedDate },
			properties: { value },
			action: {
				payload: {
					visibleRange: { lowerYear, upperYear }
				}
			}
		}) => {
			// Set highlighted year in this priority order:
			// 1. Existing highlighted
			// 2. Current value
			// 3. Current date
			const highlightedDate =
				currentHighlightedDate && currentHighlightedDate.isValid()
					? currentHighlightedDate
					: value
					? moment(value, YEARLY_FORMAT)
					: moment();

			const highlightedYear = highlightedDate.year();

			// If the date from above is not visible based on the visible range, default to lower year
			const highlightedDateVisible = inRange(
				highlightedYear,
				lowerYear,
				upperYear
			);

			updateState({
				highlightedYear: highlightedDateVisible
					? highlightedDate
					: moment({ year: lowerYear })
			});
		},
		stopPropagation: true
	},
	[SET_VISIBLE_YEAR]: {
		effect: ({ action, updateState }) => {
			updateState({ visibleYear: action.payload.year });
		},
		stopPropagation: true
	},
	[SET_DATE]: {
		effect: ({ action, properties: { onSelectDate, value: propsValue } }) => {
			const value = action.payload.date;

			const formattedValue = value.format(YEARLY_FORMAT);
			if (propsValue !== formattedValue) onSelectDate(action.payload.date);
		},
		stopPropagation: true
	},
	'NOW_BUTTON#CLICKED': {
		effect: ({
			dispatch,
			updateState,
			action: {
				payload: { action }
			},
			state: { visibleYear },
			properties: { embedded }
		}) => {
			let newYear;
			if (action === 'current') {
				const currentYear = moment().year();
				//calculate the new year in the current grid
				newYear = currentYear - (currentYear % 12) + (Number(visibleYear) % 12);
			} else {
				//action is 12 or -12
				newYear = Number(visibleYear) + action;
			}
			dispatch(SET_VISIBLE_YEAR, { year: newYear });
			//for mini calendar (embedded will be true) consider nav button change as selection
			//for date-picker embedded will be false
			if (embedded) {
				const date = moment({ year: newYear });
				dispatch(SET_DATE, { date });
				updateState({ highlightedYear: date });
			}
		},
		stopPropagation: true
	}
};
