import { t } from 'sn-translate';
import '@devsnc/sn-record-control';

export default ({ onOk, onCancel }) => {
	return (
		<div className="sn-calendar-footer">
			<div className="sn-calendar-content">
				{onCancel ? (
					<sn-record-control
						label={t('Cancel')}
						on-click={() => {
							onCancel();
						}}
						variant="tertiary"
						size="md"
						bare={false}
					/>
				) : null}
				{onOk ? (
					<sn-record-control
						label={t('OK')}
						on-click={() => {
							onOk();
						}}
						variant="secondary"
						size="md"
						bare={false}
					/>
				) : null}
			</div>
		</div>
	);
};
