import moment from 'moment';
import {
	MONTHLY_FORMAT,
	SET_VISIBLE_YEAR,
	INIT_HIGHLIGHTED_DATE,
	SET_DATE
} from '../../constants';

export default {
	[INIT_HIGHLIGHTED_DATE]: {
		effect: ({
			updateState,
			state: { highlightedMonth: currentHighlightedMonth, visibleYear },
			properties: { value }
		}) => {
			// Set highlighted month in this priority order:
			// 1. Existing highlighted
			// 2. Current value
			// 3. Current date
			const highlightedMonth =
				currentHighlightedMonth && currentHighlightedMonth.isValid()
					? currentHighlightedMonth
					: value
					? moment(value, MONTHLY_FORMAT)
					: moment();

			const highlightedYear = highlightedMonth.year();

			// If the date from above is not visible based on the visibleYear, default to first month
			const highlightedDateNotVisible = highlightedYear != Number(visibleYear);

			updateState({
				highlightedMonth: highlightedDateNotVisible
					? moment({ year: visibleYear, month: 0 })
					: highlightedMonth
			});
		},
		stopPropagation: true
	},
	[SET_VISIBLE_YEAR]: {
		effect: ({
			action: {
				payload: { year }
			},
			dispatch,
			updateState,
			properties: { value, embedded }
		}) => {
			updateState({ visibleYear: year });

			//for mini calendar (embedded will be true) consider year change as selection
			//for date-picker embedded will be false
			if (embedded) {
				const date = moment(value, MONTHLY_FORMAT).set('year', year);
				dispatch(SET_DATE, { date });
			}
		},
		stopPropagation: true
	},
	[SET_DATE]: {
		effect: ({ action, properties: { onSelectDate, value: propsValue } }) => {
			const value = action.payload.date;

			const formattedValue = value.format(MONTHLY_FORMAT);
			if (propsValue !== formattedValue) {
				onSelectDate(action.payload.date);
			}
		},
		stopPropagation: true
	}
};
