import { createCustomElement } from '@servicenow/ui-core';

import view from './view';
import styles from './styles.scss';
import { TIME_AGO_PRESETS } from '../utils/timeAgo';
import actionHandlers from './actionHandlers';

export const COMPONENT_NAME = 'sn-date-preset-panel-connected';

createCustomElement(COMPONENT_NAME, {
	view,
	styles,
	properties: {
		/**
		 * Whether to resolve relative TimeAgo to Absolute Date/Time Strings.
		 * @type {boolean}
		 */
		absolute: { default: false, schema: { type: 'boolean' } },
		/**
		 * Whether to send the between value of the TimeAgo as end value.
		 * @type {boolean}
		 */
		between: { default: false, schema: { type: 'boolean' } },
		/**
		 * Date format for the date string.
		 * @type {string}
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 *
		 * When not provided, the default format is `YYYY-MM-DD` and `YYYY-MM-DD hh:mm:ss a` when
		 * `has-time` is set to true
		 *
		 */
		format: { schema: { type: 'string' } },
		/**
		 * Whether to show Time related TimeAgo Presets
		 * @type {boolean}
		 */
		hasTime: { default: false, schema: { type: 'boolean' } },
		/**
		 * Table name for business calendars.
		 * @type {string}
		 */
		tableName: { schema: { type: 'string' } },
		/**
		 * Date value which can be a valid date or a valid TimeAgo Label.
		 * @type {string}
		 */
		value: { schema: { type: 'string' } }
	},
	dispatches: {
		/**
		 * Dispatched when Preset is changed.
		 * @type {{ label: string, relative: {start: string, end: string}, absolute: {start: string, end: string} } }}
		 */
		'SN_DATE_PRESET_PANEL#VALUE_CHANGED': {},
		/**
		 * Dispatched when Preset has field messages.
		 * @type {{ type: string, message: string }}
		 */
		'SN_DATE_PRESET_PANEL#MESSAGE_UPDATED': {}
	},
	initialState: {
		timeAgoPresets: TIME_AGO_PRESETS,
		selectedTimeAgo: {},
		infoMessage: ''
	},
	actionHandlers
});
