import React from 'react';
import { map, find, findLast } from 'lodash';
import { time } from '@devsnc/sn-controls-common';
import makeClass from 'classnames';
import { getDateRangeRows } from '../../utils/gridUtils';
import '@devsnc/sn-record-control';
import { WeekRow } from './WeekRow';
import { WEEKLY_FORMAT } from '../../utils/elements';

/**
 * Grid of days (selectable week) for the curent month (plus a few extra days from prev/next
 * month to balance out the calendar
 */
export const Weeks = ({
	date,
	highlightedDate,
	visibleMonth = date,
	start = date,
	end = date,
	format = WEEKLY_FORMAT,
	onSelectDate,
	onEndDayHover,
	onHighlightedDate,
	firstDayOfWeek = 0,
	showAdjacentMonthDays,
	rangePicker,
	language,
	hoverEnd = '',
	disabledDays,
	currentDate
}) => {
	const normalizeDate = date => date.clone().startOf('day');
	const month = getDateRangeRows(visibleMonth, firstDayOfWeek, disabledDays);
	const year = visibleMonth.year();
	const normalizedStart = normalizeDate(time(language)(start, format));
	const normalizedEnd = normalizeDate(time(language)(end, format));
	const normalizedHoverEnd = normalizeDate(time(language)(hoverEnd, format));
	const today = normalizeDate(time(language)());
	const none = !start;
	const onlyStart = start && !end;
	const normalizedCurrentDate = time(language)(currentDate, format);

	return (
		<div className="now-record-date-picker-month">
			{map(month, (week, weekIndex) => {
				const dayStartObj = find(week, { disabled: false });
				const dayEndObj = findLast(week, { disabled: false });
				let normalizedVisibleStart = visibleMonth;
				let normalizedVisibleEnd = visibleMonth;
				let disabledWeek = true;
				let isCurrentWeek = false;

				if (dayStartObj && dayEndObj) {
					disabledWeek = false;
					normalizedVisibleStart = normalizeDate(
						time(language)(visibleMonth.clone().date(dayStartObj.day), format)
					);
					normalizedVisibleEnd = normalizeDate(
						time(language)(visibleMonth.clone().date(dayEndObj.day), format)
					);

					// current date should fall between normalized visiible start and normalized visible end

					isCurrentWeek = !rangePicker
						? normalizedCurrentDate.isSameOrAfter(normalizedVisibleStart) &&
						  normalizedCurrentDate.isSameOrBefore(normalizedVisibleEnd)
						: onlyStart
						? (normalizedStart.isSameOrAfter(normalizedVisibleStart) &&
								normalizedStart.isSameOrBefore(normalizedVisibleEnd)) ||
						  normalizedStart
								.clone()
								.add(6, 'day')
								.isSame(normalizedVisibleEnd)
						: dayStartObj &&
						  dayEndObj &&
						  ((normalizedStart.isSameOrAfter(normalizedVisibleStart) &&
								normalizedStart.isSameOrBefore(normalizedVisibleEnd)) ||
								normalizedStart
									.clone()
									.add(6, 'day')
									.isSame(normalizedVisibleEnd) ||
								normalizedEnd
									.clone()
									.add(6, 'day')
									.isSame(normalizedVisibleEnd) ||
								(normalizedEnd.isSameOrAfter(normalizedVisibleStart) &&
									normalizedEnd.isSameOrBefore(normalizedVisibleEnd)));
				}

				const isSelected = none
					? false
					: onlyStart
					? (normalizedStart.isSameOrAfter(normalizedVisibleStart) &&
							normalizedStart.isSameOrBefore(normalizedVisibleEnd)) ||
					  normalizedStart
							.clone()
							.add(6, 'day')
							.isSame(normalizedVisibleEnd)
					: dayStartObj &&
					  dayEndObj &&
					  ((normalizedStart.isSameOrAfter(normalizedVisibleStart) &&
							normalizedStart.isSameOrBefore(normalizedVisibleEnd)) ||
							normalizedStart
								.clone()
								.add(6, 'day')
								.isSame(normalizedVisibleEnd) ||
							normalizedEnd
								.clone()
								.add(6, 'day')
								.isSame(normalizedVisibleEnd) ||
							(normalizedEnd.isSameOrAfter(normalizedVisibleStart) &&
								normalizedEnd.isSameOrBefore(normalizedVisibleEnd)));

				const isPreview =
					onlyStart &&
					normalizedVisibleStart.isAfter(normalizedStart) &&
					normalizedVisibleEnd.isSameOrBefore(normalizedHoverEnd);

				const isInsideRange =
					normalizedVisibleStart.isSameOrAfter(normalizedStart) &&
					normalizedVisibleEnd.isSameOrBefore(normalizedEnd) &&
					!onlyStart;

				const isHighlighted =
					!isSelected &&
					highlightedDate &&
					highlightedDate.isSameOrAfter(normalizedVisibleStart) &&
					highlightedDate.isSameOrBefore(normalizedVisibleEnd);

				const isToday =
					today.isSameOrAfter(normalizedVisibleStart) &&
					today.isSameOrBefore(normalizedVisibleEnd);

				const className = makeClass(
					'now-record-date-picker-weekly-month-week-row',
					{
						'is-selected': isSelected && !isCurrentWeek,
						'is-current-week': isCurrentWeek,
						'is-outlined': isToday,
						'is-selected-secondary':
							rangePicker && (isPreview || (!isSelected && isInsideRange)),
						'hover-secondary-selected':
							rangePicker && !isSelected && isInsideRange,
						'hover-highlighted': isInsideRange && isHighlighted,
						'hover-secondary-highlighted':
							rangePicker && !isSelected && isInsideRange && isHighlighted,
						'is-rangepicker': rangePicker
					}
				);

				const tabIndex = {
					...(isHighlighted || (isSelected && !rangePicker)
						? { tabIndex: 0 }
						: { tabIndex: -1 })
				};

				return (
					<WeekRow
						key={weekIndex}
						year={year}
						visibleMonth={visibleMonth}
						visibleStart={normalizedVisibleStart}
						disabledWeek={disabledWeek}
						week={week}
						weekIndex={weekIndex}
						weekOfYear={
							normalizedVisibleStart ? normalizedVisibleStart.format('ww') : ''
						}
						tabIndex={tabIndex}
						className={className}
						showAdjacentMonthDays={showAdjacentMonthDays}
						language={language}
						onSelectDate={onSelectDate}
						highlightedDate={highlightedDate}
						onHighlightedDate={onHighlightedDate}
						onEndDayHover={onEndDayHover}
						onlyStart={onlyStart}
						format={format}
						rangePicker={rangePicker}
					/>
				);
			})}
		</div>
	);
};
