import React from 'react';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import '@devsnc/sn-record-control';

import { DaysOfWeek } from '../../utils/gridUtils';
import { YearLabel } from './YearLabel';
import { StandardGrid } from '../../grids/standard/StandardGrid';
import { WeeklyGrid } from '../../grids/weekly/WeeklyGrid';
import { STANDARD_TYPE, WEEKLY_FORMAT } from '../../constants';

import style from './standardView.scss';

const isRTL = dir => 'rtl' === dir;

/**
 * Wrapping container for the calendar. Handles animating between 2 adjacent
 * displayed months.
 */
export class GridWrapper extends React.PureComponent {
	state = {
		date: this.props.date,
		highlightedDate: this.props.highlightedDate
	};

	static style = style;

	static properties = {
		type: {},
		visibleMonth: {},
		date: {},
		highlightedDate: {},
		start: {},
		end: {},
		format: {},
		dir: {},
		onSelectDate: {},
		onControlExit: { default: () => {} },
		onHighlightedDate: {},
		onChangeMonth: {},
		enablePreviousMonthControl: { default: true },
		enableNextMonthControl: { default: true },
		showAdjacentMonthDays: { default: true },
		onEndDayHover: {},
		sourceField: { default: '' },
		hoverEnd: { default: '' },
		hasFocus: { default: false },
		weeklyView: { default: false },
		rangePickerRef: {},
		disabledDays: {},
		enableMonthNavigationChevrons: { default: true },
		timePicker: { default: false },
		rangePicker: { default: false }
	};

	componentWillReceiveProps({ date, highlightedDate, hasFocus }) {
		this.setState({
			date,
			highlightedDate,
			slide: null,
			hasFocus
		});
	}

	render() {
		const { date, highlightedDate, hasFocus } = this.state;
		const {
			onSelectDate,
			onControlExit,
			onHighlightedDate,
			onChangeMonth,
			language,
			firstDayOfWeek,
			ariaLabel,
			configAria,
			// TODO: do this better (add a flag)
			// default start/end to date...
			start = date,
			end = date,
			format = 'MM-DD-YYYY',
			visibleMonth = date,
			enablePreviousMonthControl,
			enableNextMonthControl,
			showAdjacentMonthDays = true,
			onEndDayHover,
			hoverEnd,
			rangePickerRef,
			weeklyView = false,
			reversed = isRTL(this.props.dir),
			disabledDays,
			enableMonthNavigationChevrons,
			rangePicker,
			type,
			currentDate
		} = this.props;

		const gridProps = {
			date: date,
			highlightedDate: highlightedDate,
			disabled: this.props.disabled,
			ariaLabel: ariaLabel,
			configAria: configAria,
			visibleMonth: visibleMonth,
			onSelectDate: onSelectDate,
			onHighlightedDate: onHighlightedDate,
			start: start,
			end: end,
			format: weeklyView ? WEEKLY_FORMAT : format,
			parentRef: this.parentNode,
			rangePickerRef: rangePickerRef,
			rightMonth: enableMonthNavigationChevrons || false,
			rangePicker: rangePicker,
			firstDayOfWeek: firstDayOfWeek,
			showAdjacentMonthDays: weeklyView ? false : showAdjacentMonthDays,
			reversed: isAttrTrue(reversed),
			onEndDayHover: onEndDayHover,
			hoverEnd: hoverEnd,
			hasFocus: hasFocus,
			language: language,
			disabledDays: disabledDays,
			currentDate: currentDate
		};

		return (
			<div
				ref={node => (this.parentNode = node)}
				className="now-record-date-picker -monthly"
			>
				<YearLabel
					disabled={this.props.disabled}
					reversed={isAttrTrue(reversed)}
					date={visibleMonth}
					rangePicker={rangePicker}
					rangePickerRef={rangePickerRef}
					enablePreviousMonthControl={enablePreviousMonthControl}
					enableNextMonthControl={enableNextMonthControl}
					onSelectDate={onSelectDate}
					onControlExit={onControlExit}
					onChangeMonth={onChangeMonth}
					highlightedDate={highlightedDate}
					weeklyView={weeklyView}
					enableMonthNavigationChevrons={enableMonthNavigationChevrons}
				/>

				<DaysOfWeek
					language={language}
					firstDayOfWeek={firstDayOfWeek}
					weeklyView={weeklyView}
				/>

				{weeklyView ? (
					<WeeklyGrid {...gridProps} />
				) : (
					<StandardGrid {...gridProps} />
				)}
			</div>
		);
	}
}
