import React from 'react';
import PropTypes from 'prop-types';
import { t } from 'sn-translate';

import ControlWrapper from '@devsnc/sn-record-control-wrapper';
import {
	fieldPropTypes,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

import style from './miniCalendar.scss';
import { CalendarView } from './CalendarView';

import { WEEKLY_TYPE, STANDARD_TYPE } from './constants';

import { getFormat } from './utils/calendarUtils';

/**
 * Calendar component allowing users to select a date and optional time.
 *
 * Calls `onSelectDate` callback when user selects a date or modifies
 * the time, and calls `onOk` callback when the user selects the Ok
 * button.
 */
export class NowMiniCalendar extends React.PureComponent {
	static componentName = 'now-record-mini-calendar';
	static style = style;

	state = {
		value: this.props.value
	};

	onDateChange = value => {
		this.props.onValueChange({ value });
		this.setState({ value });
	};

	componentWillReceiveProps(nextProps) {
		if (nextProps.value !== this.state.value) {
			this.setState({
				value: nextProps.value
			});
		}
	}

	render() {
		return (
			<ControlWrapper {...this.props}>
				<CalendarView
					type={this.props.type}
					value={
						this.props.type === STANDARD_TYPE || this.props.type === WEEKLY_TYPE
							? this.props.value
							: this.state.value
					}
					format={getFormat(this.props)}
					firstDayOfWeek={this.props.firstDayOfWeek}
					onDateChange={this.onDateChange}
					reversed={this.props.reversed}
					readonly={this.props.readonly}
					language={this.props.language}
					autofocus={this.props.autofocus}
					timePicker={
						this.props.type === WEEKLY_TYPE
							? false
							: this.props.timePicker || this.props.hasTimePicker
					}
					onCancel={this.props.onCancel}
					onOk={this.props.onOk}
					disabledDates={this.props.disabledDates}
					opened={true}
					embedded={true}
				/>
				<span
					id={`${this.props.componentId}_liveLabel`}
					className="now-a11y-label"
					aria-live="polite"
				>
					{t('Selected date: {0}.', this.props.value)}
				</span>
			</ControlWrapper>
		);
	}
}

NowMiniCalendar.propTypes = {
	...fieldPropTypes,

	/**
	 * Array of disabled dates defined as
	 * `[ (DATE | DATE_RANGE)* ]`
	 *
	 * `DATE`:
	 * 	Date string matching `format` property
	 *
	 * `DATE_RANGE`:
	 * 	{ start: `DATE`, end: `DATE` } | { after: `DATE` } | { before: `DATE` }
	 *
	 * where `start`/`end` are inclusive and `after`/`before` are exclusive
	 * eg. [ '13-01-2020', { start: '15-01-2020', end: '20-01-2020' }, { before: '05-01-2020' }, { after: '25-01-2020' }]
	 *
	 * @type { Array < string | {start: string, end: string} | {before: string} | {after: string} > }
	 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
	 */
	disabledDates: PropTypes.array,

	/**
	 * Format describing how to serialize/deserialize the `value` prop.
	 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
	 *
	 * When not provided, the default format is one of:
	 *
	 * - When showing a time picker: `YYYY-MM-DD`
	 * - When not showing a time picker: `YYYY-MM-DD hh:mm:ss a`
	 */
	format: PropTypes.string,

	/**
	 * Type determines the display view of the date-range-picker.
	 * Supported values are `standard`, `weekly`, `monthly`,`quarterly` and `yearly`.
	 * Default: standard
	 */
	type: PropTypes.string,

	/**
	 * What day of the week to begin the calendar. 0 = Sunday, 1 = Monday, etc.
	 */
	firstDayOfWeek: PropTypes.oneOf([0, 1, 2, 3, 4, 5, 6]),

	/**
	 * Please use `timePicker` instead
	 */
	hasTimePicker: PropTypes.bool,

	/**
	 * Callback when user selects `Cancel` button.
	 * When it's a boolean, toggles whether or not to show the `Cancel` button.
	 *
	 * @param {SyntheticEvent} event React synthetic click event
	 */
	onCancel: PropTypes.oneOf([PropTypes.func, PropTypes.bool]),

	/**
	 * Callback when user selects `Ok` button.
	 * When it's a boolean, toggles whether or not to show the `Ok` button.
	 *
	 * @param {SyntheticEvent} event React synthetic click event
	 */
	onOk: PropTypes.oneOf([PropTypes.func, PropTypes.bool]),

	/**
	 * Callback when the user selects a new date
	 */
	onValueChange: PropTypes.func,

	/**
	 * Whether to show the time picker in addition to the calendar
	 */
	timePicker: PropTypes.bool,

	/**
	 * User language preference. Used for moment date formatting
	 */
	language: PropTypes.string
};

NowMiniCalendar.defaultProps = {
	timePicker: false,
	language: 'en',
	type: 'standard'
};

export default withValueChangeCallbacks(NowMiniCalendar);
