import React from 'react';
import PropTypes from 'prop-types';
import { noop, isObject, isFunction } from 'lodash';
import { t } from 'sn-translate';

import react from '@servicenow/ui-renderer-react';
import '@servicenow/now-record-time';
import '@devsnc/sn-record-control';

import { createCustomElement } from '@servicenow/ui-core';
import { ForwardProperties } from '@devsnc/sn-controls-common';
import { time } from '@devsnc/sn-controls-common';

import '../../preset-panel';
import { STANDARD_TYPE, STANDARD_VIEW_TAG } from '../../constants';
import { getFormat } from '../../utils/calendarUtils';
import CalendarBody from './CalendarBody';
import style from './standardView.scss';

const PRESET_VALUE_CHANGED = 'SN_DATE_PRESET_PANEL#VALUE_CHANGED';

/**
 * React entry point for the calendar.
 */
class StandarView extends React.PureComponent {
	static componentName = STANDARD_VIEW_TAG;
	static style = style;

	// Time changes are coming from now-record-time, which returns stringified, formatted
	// values. Return these as-is (but strip away the useless `event` and `name` params)
	onTimeChange = (event, name, value) => {
		this.props.onDateChange(value);
	};

	// Date changes are internal to calendar and pass up a moment date object.
	// Need to format before sending these up.
	onDateChange = date => {
		this.props.onDateChange(date.format(getFormat(this.props)));
	};

	isValidDate = () => {
		const date = time(this.props.language)(
			this.props.value,
			getFormat(this.props),
			true
		);
		return date.isValid();
	};

	onOk = event => {
		const { dispatch, onOk, name } = this.props;
		if (isFunction(onOk)) {
			onOk(event);
		}
		dispatch('NOW_RECORD_MINI_CALENDAR#ON_OK', { event, name });
	};

	onCancel = event => {
		const { dispatch, onCancel, name } = this.props;
		if (isFunction(onCancel)) {
			onCancel(event);
		}
		dispatch('NOW_RECORD_MINI_CALENDAR#ON_CANCEL', { event, name });
	};

	render() {
		const hasFooter =
			(this.props.bottomBar || this.props.timePicker) &&
			(this.props.onCancel || this.props.onOk || this.props.timePicker);

		if (!this.props.opened) {
			return null;
		}

		const { timeAgo, value, timePicker, embedded } = this.props;

		let tableName = '';
		let between = false;
		let absolute = false;

		if (isObject(timeAgo)) {
			tableName = timeAgo.tableName;
			between = timeAgo.between;
			absolute = timeAgo.absolute;
		}

		return (
			<div className={`sn-calendar${timeAgo ? ' is-preset' : ''}`}>
				{timeAgo && this.props.type == STANDARD_TYPE ? (
					<div className="date-preset-panel">
						<sn-date-preset-panel-connected
							value={value}
							tableName={tableName}
							between={between}
							absolute={absolute}
							hasTime={timePicker}
							format={getFormat(this.props)}
						/>
					</div>
				) : null}
				<div className="date-picker-panel">
					<CalendarBody
						type={this.props.type}
						value={this.props.value}
						format={getFormat(this.props)}
						firstDayOfWeek={this.props.firstDayOfWeek}
						onDateChange={this.onDateChange}
						reversed={this.props.reversed}
						readonly={this.props.readonly}
						language={this.props.language}
						autofocus={this.props.autofocus}
						utcOffset={this.props.utcOffset}
						disabledDates={this.props.disabledDates}
						timeAgo={timeAgo}
						absolute={absolute}
						embedded={embedded}
					/>
					{hasFooter && (
						<div className="sn-calendar-footer">
							{this.props.timePicker && this.props.type === STANDARD_TYPE && (
								<div className="sn-calendar-content -static">
									<now-record-time
										name={`${this.props.name}-time-picker`}
										label={t('Select Time')}
										format={getFormat(this.props)}
										value={this.props.value}
										onValueChange={this.onTimeChange}
										readonly={this.props.readonly}
										relativeTime={timeAgo && !this.isValidDate()}
										defaultZeros={this.props.defaultZeros}
									/>
								</div>
							)}
							<div className="sn-calendar-content">
								{this.props.onCancel && (
									<sn-record-control
										label={t('Cancel')}
										on-click={this.onCancel}
										variant="tertiary"
										size="md"
										bare={false}
									/>
								)}
								{this.props.onOk && (
									<sn-record-control
										label={t('OK')}
										on-click={this.onOk}
										variant="secondary"
										size="md"
										bare={false}
									/>
								)}
							</div>
						</div>
					)}
				</div>
			</div>
		);
	}
}

export default StandarView;

StandarView.propTypes = {
	value: PropTypes.string,
	format: PropTypes.string,
	firstDayOfWeek: PropTypes.oneOf([0, 1, 2, 3, 4, 5, 6]),
	onOk: PropTypes.func,
	onCancel: PropTypes.func,
	timePicker: PropTypes.bool,
	language: PropTypes.string,
	defaultZeros: PropTypes.bool,
	bottomBar: PropTypes.bool,
	timeAgo: PropTypes.oneOfType([PropTypes.bool, PropTypes.object])
};

StandarView.defaultProps = {
	timePicker: false,
	onDateChange: noop,
	language: '',
	bottomBar: true,
	opened: true,
	timeAgo: false,
	type: STANDARD_TYPE
};

// Ensure STANDARD_VIEW_TAG in src/constants matches the element name below
createCustomElement('sn-controls-standard-view', {
	type: react,
	view: ForwardProperties(StandarView),
	styles: style,
	properties: {
		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		autofocus: {},

		/**
		 * Configure the bottom portion of the calendar body (bar that contains
		 * the Ok and Cancel buttons, as well as the time field).
		 *
		 * @type {boolean}
		 */
		bottomBar: { default: true },

		/**
		 * Flag to set the time field value to zeros by default.
		 *
		 * @type {boolean}
		 */
		defaultZeros: {},

		/**
		 * Array of disabled dates defined as
		 * `[ (DATE | DATE_RANGE)* ]`
		 *
		 * `DATE`:
		 * 	Date string matching `format` property
		 *
		 * `DATE_RANGE`:
		 * 	{ start: `DATE`, end: `DATE` } | { after: `DATE` } | { before: `DATE` }
		 *
		 * where `start`/`end` are inclusive and `after`/`before` are exclusive
		 * eg. [ '13-01-2020', { start: '15-01-2020', end: '20-01-2020' }, { before: '05-01-2020' }, { after: '25-01-2020' }]
		 *
		 * @type { Array < string | {start: string, end: string} | {before: string} | {after: string} > }
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 */
		disabledDates: {
			default: [],
			schema: {
				type: 'array',
				items: [
					{ type: 'string' },
					{
						type: 'object',
						properties: { start: { type: 'string' }, end: { type: 'string' } },
						required: ['start', 'end']
					},
					{ type: 'object', properties: { before: { type: 'string' } } },
					{ type: 'object', properties: { after: { type: 'string' } } }
				]
			}
		},
		/**
		 * If this view is embedded in the page (mini-calendar) and not a popover (date-picker)
		 *
		 * @type {boolean}
		 */
		embedded: { default: false },

		/**
		 * What day of the week to begin the calendar. 1 = Sunday, 2 = Monday, etc.
		 * Can only be a value from 1 to 7
		 *
		 * @type {number}
		 */
		firstDayOfWeek: {},

		/**
		 * Format describing how to serialize/deserialize the `value` prop.
		 *
		 * When not provided, the default format is one of:
		 *
		 * - When showing a time picker: `YYYY-MM-DD hh:mm:ss a`
		 * - When not showing a time picker: `YYYY-MM-DD`
		 *
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 * @type {string}
		 */
		format: {},

		/**
		 * User language preference. Used for moment date formatting
		 *
		 * @type {string}
		 */
		language: { default: '' },

		/**
		 * Toggles whether or not to show the TimeAgo Preset Panel
		 *
		 * Passing in false will hide the panel altogether.
		 * Passing in true will show the panel with default config which is {absolute: false, between: false, tableName: ''}.
		 * Passing in a configObject will show the panel with the config as passed in.

		 * 
		 * absolute - resolve relative dates to absolute dates and return in addition to the relative dates on dispatch.
		 * between - flag to send back the 'between' instead of 'end' of relative time ago
		 * tableName - tableName for the Business Calendar Relative fields.
		 * 
		 * @type {(boolean|{absolute: boolean, between: boolean, tableName: string, value: string})}
		 *
		 */
		timeAgo: {
			default: false,
			schema: {
				oneOf: [
					{ type: 'boolean' },
					{
						type: 'object',
						properties: {
							absolute: { type: 'boolean' },
							between: { type: 'boolean' },
							tableName: { type: 'string' }
						}
					}
				]
			}
		},

		/**
		 * Type determines the display view of the date-range-picker.
		 * Supported values are `standard`, `weekly`, `monthly`,`quarterly` and `yearly`.
		 * @type {String}
		 */
		type: {
			default: 'standard',
			schema: {
				type: 'string',
				enum: ['standard', 'weekly', 'monthly', 'quarterly', 'yearly']
			}
		},

		/**
		 * Date picker function for saving value selected in the calendar.
		 * When it's a boolean, toggles whether or not to show the `Ok` button.
		 *
		 * @private
		 * @type {function | boolean}
		 */
		onOk: {},

		/**
		 * Date picker function for resetting value selected in the calendar.
		 * When it's a boolean, toggles whether or not to show the `Cancel` button.
		 *
		 * @private
		 * @type {function | boolean}
		 */
		onCancel: {},

		/**
		 * Date picker function for updating pending value when selected in the calendar.
		 *
		 * @private
		 * @type {function}
		 */
		onDateChange: { default: noop },

		/**
		 * Controls when the date picker content is displayed or not.
		 *
		 * @type {boolean}
		 */
		opened: { default: true },

		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {boolean|string}
		 * @private
		 */
		reversed: {},

		/**
		 * Whether or not to display the time picker along with the calendar.
		 *
		 * @type {boolean}
		 */
		timePicker: { default: false },

		/**
		 * The UTC offset
		 *
		 * @type {number}
		 */
		utcOffset: {},

		/**
		 * The date/date-time value. This value must be the localized time, rather than UTC time.
		 *
		 * @type {string}
		 */
		value: {}
	},
	actionHandlers: {
		[PRESET_VALUE_CHANGED]: {
			effect: ({ action, properties: { onDateChange } }) => {
				const selectedTimeAgo = action.payload;
				onDateChange(selectedTimeAgo.label, selectedTimeAgo);
			}
		}
	}
});
