import { WEEKLY_FORMAT } from '../constants';

const MONTH_OUTER_WRAPPER_CLASS = '.now-record-date-picker-month-wrapper-outer';
const MONTH_WEEK_DAY_CLASS = '.now-record-date-picker-month-week-day';
const MONTH_WEEK_WEEKLY_CLASS = '.now-record-date-picker-weekly-month-week-row';
const MONTH_VIEW_COMPONENT = 'sn-standard-month-grid';
const RECORD_CONTROL_COMPONENT = 'sn-record-control';
const RECORD_DROPDOWN_INTERNAL_COMPONENT = 'sn-record-dropdown-internal';
const SN_MONTH_HEADER = 'sn-month-header';

export const nextFocusDayElement = (parentRef, nextDate) => {
	let dayToFocus;
	const nextFocusElemSelector = `[aria-label="${nextDate.format('LL')}"]`;
	if (
		parentRef.parentNode &&
		parentRef.parentNode.host &&
		parentRef.parentNode.host.parentElement
	) {
		const snMonthViewElements = parentRef.parentNode.host.parentElement.querySelectorAll(
			MONTH_VIEW_COMPONENT
		);
		if (snMonthViewElements) {
			if (snMonthViewElements[0])
				dayToFocus = snMonthViewElements[0].shadowRoot.querySelector(
					nextFocusElemSelector
				);
			if (!dayToFocus && snMonthViewElements[1]) {
				dayToFocus = snMonthViewElements[1].shadowRoot.querySelector(
					nextFocusElemSelector
				);
			}
		}
	} else {
		dayToFocus = parentRef.querySelector(nextFocusElemSelector);
	}
	return dayToFocus;
};

export const nextFocusWeekElement = (nextDate, rangePickerRef, parentRef) => {
	const nextFocusElemSelector = `[id="${nextDate.format(WEEKLY_FORMAT)}"]`;
	let weekToFocus;
	if (rangePickerRef) {
		parentRef = rangePickerRef.current.querySelector(`.date-pickers`);
		const snMonthViewElements = parentRef.querySelectorAll(
			MONTH_VIEW_COMPONENT
		);
		if (snMonthViewElements) {
			if (snMonthViewElements[1])
				weekToFocus = snMonthViewElements[1].shadowRoot.querySelector(
					nextFocusElemSelector
				);
			if (!weekToFocus && snMonthViewElements[0]) {
				weekToFocus = snMonthViewElements[0].shadowRoot.querySelector(
					nextFocusElemSelector
				);
			}
		}
	} else if (parentRef) {
		weekToFocus = parentRef.querySelector(nextFocusElemSelector);
	}
	return weekToFocus;
};

export const nextFocusControlElement = (currentTarget, rightMonth) => {
	let controlToFocus;
	if (currentTarget.parentNode) {
		const headerControls = currentTarget.parentNode.querySelector(
			`${SN_MONTH_HEADER}`
		);

		if (headerControls) {
			const nowControlToFocus = headerControls.shadowRoot.querySelectorAll(
				'now-button'
			);
			const headerControlToFocus = rightMonth
				? nowControlToFocus[0]
				: nowControlToFocus[nowControlToFocus.length - 1];

			if (headerControlToFocus) {
				controlToFocus = headerControlToFocus.shadowRoot.querySelector(
					'button'
				);
			}
		}
	}
	return controlToFocus;
};

export const handleRangePickerMonthTab = (
	rangePickerRef,
	highlightedDate,
	event,
	weeklyView
) => {
	let nextCtrlWrapperLeft;
	let nextCtrlWrapperRight;
	let nextCtrlWrapper;
	let nextDayCtrl;
	const navClass = weeklyView ? MONTH_WEEK_WEEKLY_CLASS : MONTH_WEEK_DAY_CLASS;

	const monthViews = rangePickerRef.current.querySelectorAll(
		MONTH_VIEW_COMPONENT
	);
	if (monthViews && monthViews.length > 0) {
		nextCtrlWrapper = nextCtrlWrapperLeft = monthViews[0].shadowRoot.querySelector(
			MONTH_OUTER_WRAPPER_CLASS
		);
		nextCtrlWrapperRight = monthViews[1].shadowRoot.querySelector(
			MONTH_OUTER_WRAPPER_CLASS
		);
	}
	if (nextCtrlWrapperLeft && nextCtrlWrapperRight) {
		let nextFocusElemSelector = `[aria-label="${highlightedDate.format(
			'LL'
		)}"]`;

		if (weeklyView) {
			nextFocusElemSelector = `[id="${highlightedDate.format(WEEKLY_FORMAT)}"]`;
		}

		nextDayCtrl = nextCtrlWrapperLeft.querySelector(nextFocusElemSelector);
		if (!nextDayCtrl) {
			nextDayCtrl = nextCtrlWrapperLeft.querySelector(
				`${navClass}.is-selected`
			);
		}
		if (!nextDayCtrl) {
			if (highlightedDate) {
				nextDayCtrl = nextCtrlWrapperRight.querySelector(nextFocusElemSelector);
				if (!nextDayCtrl) {
					nextDayCtrl = nextCtrlWrapperRight.querySelector(
						`${navClass}.is-selected`
					);
				}
				if (nextDayCtrl) {
					nextCtrlWrapper = nextCtrlWrapperRight;
				}
			}
			if (!nextDayCtrl) {
				nextDayCtrl = nextCtrlWrapperLeft.querySelector(
					`${navClass}:not(.is-disabled)`
				);
			}
		}
		if (nextDayCtrl) {
			event.stopPropagation();
			event.preventDefault();
			nextCtrlWrapper.tabIndex = 0;
			nextDayCtrl.tabIndex = 0;
			nextDayCtrl.focus();
		}
	}
};

export const handleRangePickerYearTab = (
	rangePickerRef,
	event,
	highlightedDate,
	weeklyView,
	enableMonthNavigationChevrons
) => {
	let nextCtrlWrapper;
	let monthOuterWrapper;
	const monthViews = rangePickerRef.current.querySelectorAll(
		MONTH_VIEW_COMPONENT
	);
	if (monthViews && monthViews.length > 0) {
		monthOuterWrapper = monthViews[0].shadowRoot.querySelector(
			MONTH_OUTER_WRAPPER_CLASS
		);
		if (event.shiftKey) {
			//right month - move to left month year dropdown
			if(enableMonthNavigationChevrons){
				nextCtrlWrapper = monthViews[0].shadowRoot.querySelector(
					`${RECORD_DROPDOWN_INTERNAL_COMPONENT}`
				);
			}else{ //left month - move to 1st day in left calendar
				handleRangePickerMonthTab(
					rangePickerRef,
					highlightedDate,
					event,
					weeklyView
				);
				return;
			}
		} else {
			//right month - move to left chevron in left month
			if(!enableMonthNavigationChevrons){
				nextCtrlWrapper = monthViews[1].shadowRoot.querySelector(
					`${RECORD_DROPDOWN_INTERNAL_COMPONENT}`
				);
			}
			
		}
	}
	if (nextCtrlWrapper && monthOuterWrapper) {
		const nextctrl = nextCtrlWrapper.shadowRoot.querySelector('now-dropdown');
		if (nextctrl) {
			event.stopPropagation();
			event.preventDefault();
			monthOuterWrapper.tabIndex = -1;
			nextctrl.focus();
		}
	}
};

export const handleRangePickerDayTab = (rangePickerRef, event) => {
	let nextCtrlWrapper;
	let monthOuterWrapper;

	const monthViews = rangePickerRef.current.querySelectorAll(
		MONTH_VIEW_COMPONENT
	);


	if (event.shiftKey && rangePickerRef) {
		if (monthViews && monthViews.length > 0) {
			monthOuterWrapper = monthViews[0].shadowRoot.querySelector(
				MONTH_OUTER_WRAPPER_CLASS
			);
			nextCtrlWrapper = monthViews[1].shadowRoot.querySelector(SN_MONTH_HEADER);
			
		}
		const nextctrl = nextCtrlWrapper.shadowRoot.querySelectorAll('now-button')[2];

		if (nextCtrlWrapper && monthOuterWrapper) {
			if (nextctrl) {
				event.stopPropagation();
				event.preventDefault();
				event.currentTarget.tabIndex = -1;
				monthOuterWrapper.tabIndex = -1;
				nextctrl.focus();
			}
		}
	} else {
		
		if (monthViews && monthViews.length > 0) {
			monthOuterWrapper = monthViews[0].shadowRoot.querySelector(
				MONTH_OUTER_WRAPPER_CLASS
			);
			nextCtrlWrapper = monthViews[0].shadowRoot.querySelector(
				`${RECORD_DROPDOWN_INTERNAL_COMPONENT}`
			);
		}
	
		const nextctrl = nextCtrlWrapper.shadowRoot.querySelector('now-dropdown');

		const footer = rangePickerRef.current.querySelector(
			'.date-picker-panel-footer'
		);

		if (footer) {
			let time = footer.querySelector('now-record-time');
			let confirmation = footer.querySelector(
				'.date-range-picker-confirmation'
			);

			if (time) {
				time.shadowRoot.querySelector('input').focus();
			} else if (confirmation) {
				confirmation.querySelector('now-button').focus();
			}
		}else{
				event.stopPropagation();
				event.preventDefault();
				event.currentTarget.tabIndex = -1;
				monthOuterWrapper.tabIndex = -1;
				nextctrl.focus();
		}
	}
};

export const handleRangePickerWeekTab = (rangePickerRef, event) => {
	let nextCtrlWrapper;
	let prevCtrlWrapper;
	let monthOuterWrapper;

	const monthViews = rangePickerRef.current.querySelectorAll(
		MONTH_VIEW_COMPONENT
	);

	if (monthViews && monthViews.length > 0) {
		monthOuterWrapper = monthViews[0].shadowRoot.querySelector(
			MONTH_OUTER_WRAPPER_CLASS
		);
		prevCtrlWrapper = monthViews[0].shadowRoot.querySelector(
			`${RECORD_CONTROL_COMPONENT}`
		);
		nextCtrlWrapper = monthViews[1].shadowRoot.querySelector(
			`${RECORD_CONTROL_COMPONENT}`
		);
	}

	const nextctrl = nextCtrlWrapper.shadowRoot.querySelector('now-button');
	const prevctrl = prevCtrlWrapper.shadowRoot.querySelector('now-button');

	if (event.shiftKey && rangePickerRef) {
		if (nextCtrlWrapper && monthOuterWrapper) {
			if (nextctrl) {
				event.stopPropagation();
				event.preventDefault();
				event.currentTarget.tabIndex = -1;
				monthOuterWrapper.tabIndex = -1;
				nextctrl.focus();
			}
		}
	} else {
		const footer = rangePickerRef.current.querySelector(
			'.date-picker-panel-footer'
		);

		if (footer) {
			let time = footer.querySelector('now-record-time');
			let confirmation = footer.querySelector(
				'.date-range-picker-confirmation'
			);

			if (time) {
				time.shadowRoot.querySelector('input').focus();
			} else if (confirmation) {
				confirmation.querySelector('now-button').focus();
			}
		} else {
			if (!event.shiftKey && rangePickerRef) {
				if (prevCtrlWrapper && monthOuterWrapper) {
					if (prevctrl) {
						event.stopPropagation();
						event.preventDefault();
						event.currentTarget.tabIndex = -1;
						monthOuterWrapper.tabIndex = -1;
						prevctrl.focus();
					}
				}
			}
		}
	}
};
